import { showError } from '../../UI/uiReducer'
import {
  SET_CHARACTER_GENDER_START,
  SET_CHARACTER_GENDER_SUCCESS,
  SET_CHARACTER_GENDER_ERROR
} from '../characterEditorConstants'


export function setGender(gender) {
  return (dispatch, getState, unity) => {
    dispatch({
      type: SET_CHARACTER_GENDER_START,
      payload: { gender }
    })

    return unity.callAsync('CreateNewCharacter', gender)
      .then(character => unity.callAsync('LoadCharacter', character))
      .then(character => {
        dispatch({
          type: SET_CHARACTER_GENDER_SUCCESS,
          payload: {character}
        })
        return character
      })
      .catch((error) => {
        dispatch({type: SET_CHARACTER_GENDER_ERROR, payload: {error}})
        dispatch(showError('Failed to set gender on character', error))
      })
  }
}



