/**
 * Unity / Coherent UI integration. All calls to Unity should go via this, so they can be mocked.
 *
 * @param {Emitter} engine The coherent integration engine
 * @constructor
 * @class services.local.UnityIntegration
 */

function deserializeFromCoherent(data) {
  if (typeof data === 'string' && (data[0] == '{' || data[0] == '[')) {
    try {
      return JSON.parse(data)
    } catch (error) {
      console.log("Failed to parse json from unity", error, data)
      return data
    }
  } else {
    return data
  }
}


function serializeForCoherent(value) {
  if (typeof value === 'object') {
    return JSON.stringify(value)
  } else {
    return value
  }
}



export default (engine) => {

  /* CALLS TO UNITY
   ======================================================================= */

  /**
   * Trigger a fire and forget type of operation in unity, will log
   * any error returned to the console.
   *
   * @see UnityIntegration.call for details about parameters
   * @returns nothing
   */
  function trigger() {
    // make sure errors are logged by putting a done on the returned promise
    this.call.apply(this, arguments).done()
  }


  /**
   * Call a method in Unity and retrieve the result asynchronously.
   * The method must have been registered in Unity with CoherentUI.View.BindCall.
   *
   * Important: if you are not taking care of the returned promise you _MUST_
   *            use UnityIntegration.trigger instead or else any error
   *            that comes back will be silently swallowed. If calling this
   *            _you_ are responsible for handling errors (so be careful to choose
   *            the correct method done/then on the promise)
   *
   * @param {string} name The name of the method to call.
   * @param {*} [arguments] Parameters to be passed to the method,
   *                        if any of them is an object it will be converted to a json string before sent
   * @returns {Promise} The future response from the game engine
   */
  function call() {
    let jsonifiedArgs = Array.prototype.map.call(arguments, serializeForCoherent)

    // delegate to the engine
    return Promise.resolve(engine.call.apply(engine, jsonifiedArgs))
      .then(deserializeFromCoherent)
  }


// the unfullfilled promises from callAsync
// note that there is no time out handling so if a promise is never
// completed it will stay here forever and leak a little memory
  let asyncRegistry = []
// there might be a race condition where the result arrives really quickly (even before the
// id from the original call) so we need to have keep track of those and invoke a bit backwards
  let alreadyArrived = []

  /**
   * Call a method in Unity that returns a JSPromise and bridge that JSPromise on the C# side to
   * a promise on this side - will return a promise that is completed when the promise on the
   * C# side is completed.
   *
   * There is no timeout handling here, if a JSPromise never completes the promise returned from
   * this method will also never complete.
   *
   * If the resolve/reject data is json it will be deserialized into an object for you
   */
  function callAsync(name) {

    return call.apply(this, arguments).then(
      function asyncSuccess(promiseDetails) {
        let promiseId = promiseDetails.jsPromiseId
        if (promiseId) {
          return new Promise(function(resolve, reject) {
            let alreadyHere = alreadyArrived[promiseId]
            if (alreadyHere) {
              if (alreadyHere.resolve) resolve(alreadyHere.resolve)
              else reject(alreadyHere.reject)
              alreadyArrived[promiseId] = undefined
            } else {
              asyncRegistry[promiseId] = {
                resolve: resolve,
                reject: reject
              }
            }
          })
        } else {
          throw new Error("Tried to call " + name + " as an async C# callback but it does not return a JSPromise. (Got " + promiseDetails + " back")
        }
      }
    )
  }

// listeners for the special events back from JSPromise
  engine.on('_promiseResolved', function promiseResolved(rawReply) {
    let reply = deserializeFromCoherent(rawReply)
    let unfulfilled = asyncRegistry[reply.id]
    if (unfulfilled) {
      let resultJson = deserializeFromCoherent(reply.data)
      unfulfilled.resolve(resultJson)
      asyncRegistry[reply.id] = undefined
    } else {
      alreadyArrived[reply.id] = { resolve: reply.data }
    }
  })

  engine.on('_promiseRejected', function promiseRejected(rawReply) {
    let reply = deserializeFromCoherent(rawReply)
    let unfulfilled = asyncRegistry[reply.id]
    if (unfulfilled) {
      unfulfilled.reject(reply.data)
      asyncRegistry[reply.id] = undefined
    } else {
      alreadyArrived[reply.id] = { reject: reply.data }
    }
  })

  /**
   * Listen for event
   *
   * @param name    The event name
   * @param handler The callback
   * @return {{clear: function}} an object with a clear function that cancel/stops the listener
   */
  function on(name, handler) {
    return engine.on(name, function() {
      const args = Array.prototype.slice.call(arguments)
      handler(...args.map(arg => deserializeFromCoherent(arg)))
    })
  }

  /**
   * Do we have access to some kind of Unity?
   *
   * @returns {boolean}
   */
  function isAvailable() {
    return engine && engine.IsAttached
  }
  return {
    trigger,
    call,
    callAsync,
    on,
    isAvailable
  }
}



