import { showError } from '../../UI/uiReducer'
import {
  FETCH_CHARACTERS_START,
  FETCH_CHARACTERS_SUCCESS,
  FETCH_CHARACTERS_ERROR
} from '../characterListConstants'

const createGenericAction = (type) => { return {type} }

export default function () {
  return (dispatch, getState, unity) => {
    dispatch(createGenericAction(FETCH_CHARACTERS_START))
    return unity.call('GetCharacters')
      .then(characters => {
        return dispatch({
          type: FETCH_CHARACTERS_SUCCESS,
          payload: {characters}
        })
      })
      .catch((error) => {
        dispatch(createGenericAction(FETCH_CHARACTERS_ERROR))
        dispatch(showError('Failed to get characters', error))
      })
  }
}



