import {
  RENAME_CHARACTER_START,
  RENAME_CHARACTER_SUCCESS,
  RENAME_CHARACTER_ERROR,
} from '../characterListConstants'

export default function (id, newName) {
  return (dispatch, getState, unity) => {
    dispatch({type: RENAME_CHARACTER_START})
    return unity.call('RenameCharacter', id, newName)
      .then(()=> {
        return dispatch(
          {
            type: RENAME_CHARACTER_SUCCESS,
            payload: {id, newName}
          })
      })
      .catch((error) => {
        dispatch({type: RENAME_CHARACTER_ERROR})
        dispatch(showError('Failed to rename character', error))
      })
  }
}



