import { findInstructionIndexById } from '../utils/manuscriptUtils'
import { MOVE_INSTRUCTION, MOVE_SCENE } from '../manuscriptEditorConstants'


/**
 * Move a scene up or down. Also moves it's child instructions
 *
 * @param {string}  sceneId The instruction to move
 * @param {number}  direction  Can be either 1 (down) or -1 (up)
 */
export const moveScene = (sceneId, direction) => {
  return (dispatch, getState, unity) => {
    const scenes = getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    const sceneIndex = scenes.findIndex(s => s.getIn(['parameters', 'GUID']) === sceneId)
    const sceneToMove = scenes.get(sceneIndex)
    const sceneToSwitchPlaceWith = scenes.get(sceneIndex + direction)
    const indexFrom = findInstructionIndexById(sceneId, scenes)
    const indexTo = sceneToSwitchPlaceWith === undefined ? 0 : findInstructionIndexById(sceneToSwitchPlaceWith.getIn(['parameters', 'GUID']), scenes)

    // Move scene
    unity.call('MoveInstruction', indexFrom, indexTo)

    // Move all the child instructions as well
    sceneToMove.get('childInstructions').forEach((i, index) => {
      unity.call('MoveInstruction', indexFrom + index + 1, indexTo + index + 1)
    })

    // Trigger move in the ui
    dispatch({
      type: MOVE_SCENE,
      payload: { sceneId, sceneIndex, direction }
    })
  }
}


/**
 * Move an instruction up or down
 *
 * @param {string}  instructionId The instruction to move
 * @param {number}  direction  Can be either 1 (down) or -1 (up)
 */
export const moveInstruction = (instructionId, direction) => {
  return (dispatch, getState, unity) => {
    const scenes = getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    const indexFrom = findInstructionIndexById(instructionId, scenes)
    const indexTo = indexFrom + direction
    unity.call('MoveInstruction', indexFrom, indexTo)

    dispatch({
      type: MOVE_INSTRUCTION,
      payload: { instructionId, index: indexFrom, direction }
    })
  }
}



