import {
  ID_KEY,
  INSTRUCTIONS_KEY
} from './../manuscriptStateConstants'

import { validateInstruction, validateScene } from './../utils/validation'

import {
  findInstructionById,
  modifyInstructions
} from './../utils/manuscriptUtils'


export const updateScene = (manuscript, sceneInstruction, instructionId, content) => {
  const scenes = manuscript.getIn(INSTRUCTIONS_KEY)

  const updatedScenes = scenes.map(s => {
    if (s.getIn(ID_KEY) === instructionId) {
      return validateScene(sceneInstruction, content)
    } else {
      return s
    }
  })

  return manuscript.setIn(INSTRUCTIONS_KEY, updatedScenes)
}

export const updateInstruction = (manuscript, instruction, instructionId, content) => {
  const scenes = manuscript.getIn(INSTRUCTIONS_KEY)
  const parentSceneId = manuscript.getIn(['manuscript', 'currentSceneId'])
  const parentScene = findInstructionById(parentSceneId, scenes)
  const updatedInstructions = modifyInstructions(scenes, (i) => {
    if (i.getIn(ID_KEY) === instructionId) {
      return validateInstruction(instruction, content, parentScene.get('validForActions'), parentScene.get('charactersInScene'))
    }
    return i
  })

  return manuscript.setIn(INSTRUCTIONS_KEY, updatedInstructions)
}



