import React from 'react'
import pure from 'recompose/pure'
import SvgIcon from 'material-ui/SvgIcon'

let SceneIcon = (props) => (
  <SvgIcon {...props} viewBox="0 0 50 50" >
    <g>
      <polygon points="3,45 2,22 48,20 47,47 	" />
      <polygon points="2,23.3 0,16 46,2 48,12 	" />
    </g>
  </SvgIcon>
)
SceneIcon = pure(SceneIcon)
SceneIcon.displayName = 'SceneIcon'
SceneIcon.muiName = 'SvgIcon'

export default SceneIcon



