import { fromJS } from 'immutable'
import { validateScene } from './../utils/validation'
import { groupInstructionsByScenes, saveCharactersInScene } from './../utils/manuscriptUtils'


export const loadManuscript = (payload) => {

  const manuscript = fromJS({
    id: payload.id,
    editMode: false,
    manuscript: payload.manuscript
  })

  const loadedInstructions = manuscript.getIn(['manuscript', 'instructions'])
  const content = fromJS(payload.content)
  const allCharacters = content.get('characters')

  const sceneInstructions = groupInstructionsByScenes(loadedInstructions)
    .map(s => saveCharactersInScene(s, allCharacters))
    .map(s => validateScene(s, content))

  return manuscript
    .setIn(['manuscript', 'instructions'], sceneInstructions)
    .deleteIn(['manuscript', 'selectedIndex'])
}




