import React, {PropTypes, Component} from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import ImmutablePropTypes from 'react-immutable-proptypes'
import {List} from 'immutable'


import {NavigationControls} from './NavigationControls'
import {PickerPane} from './Pickers/PickerPane'

export class CharacterControlPane extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  render() {
    const {editorUIData} = this.props

    const viewData = editorUIData.get('currentView')
    const previousView = viewData.get('previousView')
    const currentView = viewData.get('currentView')
    const nextView = viewData.get('nextView')

    const categories = editorUIData.get('categories', List())
    const displayType = editorUIData.get('displayType', 'normal')
    const variations = editorUIData.get('variations', List())

    return (
      <div className="character-control-pane">
        <NavigationControls
          previousViewName={previousView.get('name', '')}
          currentViewName={currentView.get('name')}
          nextViewName={nextView.get('name', '')}
          gotoView={this.props.gotoViewCategory}
        />
        <PickerPane categories={categories}
          displayType={displayType}
          variations={variations}
          currentCharacter={this.props.currentCharacter}
          onGoBack={this.props.handleGoBack}
        />
      </div>
    )
  }
}

CharacterControlPane.propTypes = {
  editorUIData: ImmutablePropTypes.mapContains({
    currentView: ImmutablePropTypes.mapContains({
      previousView: ImmutablePropTypes.mapContains({
        name:PropTypes.string.isRequired
      }).isRequired,
      currentView: ImmutablePropTypes.mapContains({
        name:PropTypes.string.isRequired
      }).isRequired,
      nextView: ImmutablePropTypes.mapContains({
        name:PropTypes.string.isRequired
      }).isRequired,
    }).isRequired,
    categories: ImmutablePropTypes.list,
    displayType: PropTypes.string,
    variations: ImmutablePropTypes.list
  }).isRequired,
  gotoViewCategory: PropTypes.func.isRequired,
  currentCharacter: ImmutablePropTypes.map,
  handleGoBack: PropTypes.func.isRequired
}



