import { goBack } from 'react-router-redux'

import {
  CLOSE_CHARACTER_EDITOR_SUCCESS,
  CLOSE_CHARACTER_EDITOR_START
} from '../characterEditorConstants'

export default function () {
  return (dispatch, getState, unity) => {
    const character = getState().getIn(['currentCharacter', 'definition'])
    dispatch({type:CLOSE_CHARACTER_EDITOR_START})
    return unity.callAsync("HideCharacterCreatorWindow")
      .then(()=>{
        dispatch(goBack())
        return dispatch({
          type: CLOSE_CHARACTER_EDITOR_SUCCESS,
          meta: {
            metrics: {
              name: 'Save Character',
              data: character.toJS()
            }
          }
        })
      })
  }
}



