import { goBack } from 'react-router-redux'
import { showError } from '../../UI/uiReducer'
import {
  SET_ITEM_VARIATION
} from '../characterEditorConstants'

export const setItemVariation = (id, category) => {
  return (dispatch, getState, unity) => {

    dispatch({
      type: SET_ITEM_VARIATION,
      payload: {
        id,
        category
      }
    })

    const characterData = getState().get('currentCharacter').toJS()
    return unity.callAsync("UpdateActiveCharacter", characterData)
      .catch(() => {
        dispatch(goBack())
        dispatch(showError('Failed to select character item variation', error))
      })
  }
}



