import {
  ADD_MANUSCRIPT_START,
  ADD_MANUSCRIPT_SUCCESS,
  ADD_MANUSCRIPT_ERROR
} from '../manuscriptListConstants'
import { push } from 'react-router-redux'

export default function (name = "placeholder name") {
  return (dispatch, getState, unity) => {
    dispatch({type: ADD_MANUSCRIPT_START})
    return unity.callAsync('AddManuscript', name, true)
      .then((manuscript) => {
        dispatch({
          type: ADD_MANUSCRIPT_SUCCESS,
          payload: { manuscript }
        })

        dispatch(push(`plots/${manuscript.id}`))

        return manuscript
      }).catch((error) => {
        return dispatch({
          type: ADD_MANUSCRIPT_ERROR,
          payload: { error }
        })
      })
  }
}



