import { Map } from 'immutable'

import {
  PREVIEW_PLOT_START,
  PREVIEW_PLOT_STOP,
  SHOW_SUBTITLES
} from './../manuscriptEditorConstants'

const initialState = Map({
  isPlaying: false,
  subtitle: false
})

export default function (player = initialState, action) {
  switch(action.type) {

    case PREVIEW_PLOT_START:
      return player.set('isPlaying', true)

    case SHOW_SUBTITLES:
      return player.set('subtitle', true)

    case PREVIEW_PLOT_STOP:
      return initialState

    default:
      return player
  }
}



