import React, { PropTypes, Component } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import IconButton from 'material-ui/IconButton'
import LeftArrowAsset from 'material-ui/svg-icons/navigation/arrow-back'
import RightArrowAsset from 'material-ui/svg-icons/navigation/arrow-forward'
import {Toolbar, ToolbarGroup, ToolbarTitle} from 'material-ui/Toolbar'

export class NavigationControls extends Component {

  constructor(props) {
    super(props)
    this.state = {
      value: 3
    }
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleChange(event, index, value){
    this.setState({value})
  }

  render () {
    const { gotoView, previousViewName, nextViewName } = this.props
    const handlePrevious = () => {
      gotoView(previousViewName)
    }
    const handleNext = () => {
      gotoView(nextViewName)
    }

    return (
      <Toolbar >
        <ToolbarGroup>
          <ToolbarTitle text={this.props.currentViewName} />
        </ToolbarGroup>
        <ToolbarGroup lastChild>
          <IconButton onClick={handlePrevious} disabled={previousViewName === ''} >
            <LeftArrowAsset />
          </IconButton>
          <IconButton onClick={handleNext} disabled={nextViewName === ''}>
            <RightArrowAsset />
          </IconButton>
        </ToolbarGroup>
      </Toolbar>
    )

  }
}

NavigationControls.propTypes = {
  previousViewName: PropTypes.string,
  currentViewName: PropTypes.string.isRequired,
  nextViewName: PropTypes.string,
  gotoView: PropTypes.func.isRequired
}



