import { List } from 'immutable'

import {
  ID_KEY,
  INSTRUCTIONS_KEY
} from '../manuscriptStateConstants'

import {
  findInstructionIndex,
  findInstructionById
} from './../utils/manuscriptUtils'


export const addSceneInstruction = (manuscript, newScene) => {

  const currentSceneId = manuscript.getIn(['manuscript', 'currentSceneId'])
  const scenes = manuscript.getIn(INSTRUCTIONS_KEY, List([]))
  const currentSceneIndex = findInstructionIndex(scenes, currentSceneId)
  const atTheEnd = currentSceneIndex === (scenes.count()-1) ? true : false

  if (scenes.isEmpty()) { // We currently have an empty manuscript
    return manuscript
      .setIn(['manuscript', 'selectedId'], newScene.getIn(ID_KEY))
      .setIn(['manuscript', 'currentSceneId'], newScene.getIn(ID_KEY))
      .setIn(INSTRUCTIONS_KEY, scenes.push(newScene.set('selected', true)))
  } else if (atTheEnd) { // Last scene selected
    return manuscript
      .setIn(INSTRUCTIONS_KEY, scenes.push(newScene))
  } else { // Somewhere in the middle
    return manuscript
      .setIn(INSTRUCTIONS_KEY, scenes.insert(currentSceneIndex + 1, newScene))
  }
}

export const addInstruction = (manuscript, instruction) => {

  const scenes = manuscript.getIn(INSTRUCTIONS_KEY, List([]))
  const currentSceneId = manuscript.getIn(['manuscript', 'currentSceneId'])
  const selectedId = manuscript.getIn(['manuscript', 'selectedId'])
  const scene = findInstructionById(currentSceneId, scenes)
  const childInstructions = scene.get('childInstructions')
  const sceneIndex = findInstructionIndex(scenes, currentSceneId)
  const instructionIndex = findInstructionIndex(childInstructions, selectedId)
  const atTheEnd = instructionIndex === (childInstructions.count()-1) ? true : false

  let updatedSceneInstruction
  if (childInstructions.isEmpty()) { // We currently have an empty scene
    updatedSceneInstruction = scene.set('childInstructions', List([instruction]))
  } else if (atTheEnd) { // Last scene selected
    updatedSceneInstruction = scene.set('childInstructions', childInstructions.push(instruction))
  } else { // Somewhere in the middle
    updatedSceneInstruction = scene.set('childInstructions', childInstructions.insert(instructionIndex + 1, instruction))
  }

  return manuscript.setIn(INSTRUCTIONS_KEY, scenes.update(sceneIndex, () => updatedSceneInstruction))
}



