import React from 'react'
import pure from 'recompose/pure'
import SvgIcon from 'material-ui/SvgIcon'

let ArrowUpIcon = (props) => (
  <SvgIcon {...props} viewBox="0 0 50 50" >
    <g>
      <polygon points="25.2,5.8 42.2,24 45.8,20.4 25.2,0 4.2,20 7.8,23.6" />
      <g>
        <polygon points="27.2,3 28,47 23,47 23.2,3" />
      </g>
    </g>
  </SvgIcon>
)
ArrowUpIcon = pure(ArrowUpIcon)
ArrowUpIcon.displayName = 'ArrowUpIcon'
ArrowUpIcon.muiName = 'SvgIcon'

export default ArrowUpIcon





