import React from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import { connect } from 'react-redux'
import ImmutablePropTypes from 'react-immutable-proptypes'
import moment from 'moment'
import FloatingActionButton from 'material-ui/FloatingActionButton'
import ContentAdd from 'material-ui/svg-icons/content/add'

import * as manuscriptActionCreators from '../actionCreators/manuscriptListActionCreators'
import { ManuscriptListItemContainer } from './ManuscriptListItem'
import { ManuscriptRenameContainer } from './ManuscriptRename'
import { ManuscriptDeleteContainer } from './ManuscriptDelete'

export class ManuscriptList extends React.Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  createManuscripts = (manuscripts) => {
    return manuscripts
      .toList()
      .sortBy((manuscript) => new Date(manuscript.get('dateCreated')))
      .reverse()
      .map((manuscript) => (
        <ManuscriptListItemContainer
          className="manuscript"
          key={manuscript.get('id')}
          id={manuscript.get('id')}
          name={manuscript.get('name')}
          thumbnail={manuscript.get('thumbnail', '')}
        />
      ))
  }

  handleAddManuscript = () => {
    const name = moment().format('LLLL')
    this.props.handleAddManuscript(name)
  }

  render() {
    return (
      <div style={styles.root}>
        <div style={styles.gridList}>
          {this.createManuscripts(this.props.manuscripts)}
        </div>
        <FloatingActionButton id="create_new_manuscript"
          onClick={this.handleAddManuscript}
          style={styles.actionButton}
          secondary
        >
          <ContentAdd />
        </FloatingActionButton>
        <ManuscriptRenameContainer />
        <ManuscriptDeleteContainer />
      </div>
    )
  }
}

ManuscriptList.propTypes = {
  manuscripts: ImmutablePropTypes.map.isRequired,
  handleAddManuscript: React.PropTypes.func.isRequired
}

const mapStateToProps = (state) => {
  return {
    manuscripts: state.get('manuscripts')
  }
}


export const ManuscriptListContainer = connect(
  mapStateToProps,
  {
    handleAddManuscript: manuscriptActionCreators.addManuscript,
  }
)(ManuscriptList)

const styles = {
  root: {
    height: '100%'
  },
  gridList: {
    display: 'flex',
    flexWrap: 'wrap',
    width: '100%',
    height: '100%',
    overflowY: 'auto',
    paddingTop: 56
  },
  actionButton: {
    position: 'fixed',
    bottom: 20,
    right: 20
  }
}



