import React, { Component, PropTypes } from 'react'
import ImmutablePropTypes from 'react-immutable-proptypes'

import DialogueIcon from '../SvgIcons/DialogueIcon'
import { ListItem } from 'material-ui/List'
import TextField from 'material-ui/TextField'

import * as constants from '../../manuscriptEditorConstants'
import { InstructionControlsContainer } from '../InstructionControls'

import * as styles from '../styles/instructionListStyles'

export default class DialogueInstruction extends Component {

  _createContentNodes = (mode) => {
    const instruction = this.props.instruction
    const GUID = instruction.getIn(['parameters', 'GUID'])
    const name = instruction.getIn(['parameters', 'character', 'text'])
    const expression = instruction.getIn(['parameters', 'expression', 'text'])
    const text = instruction.getIn(['parameters', 'text', 'text'])

    switch (mode) {
      case constants.INSTRUCTION_MODE_EDITING:
        return <TextField id={GUID} value={text} />

      default:
        return (
          <div>
            <span style={{textTransform: 'uppercase', display: 'block', fontWeight: 'bold'}}>
              {name} (<span style={{textTransform: 'lowercase'}}>{expression}</span>)
            </span>
            <span>{text}</span>
            <InstructionControlsContainer id={GUID}
              isVisible={this.props.mode === constants.INSTRUCTION_MODE_OPEN}
              onEdit={this.props.onEditInstruction}
            />
          </div>
        )
    }
  }

  render () {
    const instruction = this.props.instruction
    const GUID = instruction.getIn(['parameters', 'GUID'])
    const content = this._createContentNodes(this.props.mode)
    return (
      <ListItem key={GUID}
        className="dialog-instruction-container"
        primaryText={content}
        leftIcon={<DialogueIcon style={styles.subInstructionIcon} />}
        style={styles.subInstructionStyle(this.props.mode === constants.INSTRUCTION_MODE_OPEN)}
        innerDivStyle={styles.subInstructionInnerStyle}
        onTouchTap={this.props.onHandleSelect}
      />
    )
  }
}

DialogueInstruction.propTypes = {
  instruction: ImmutablePropTypes.contains({
    type: PropTypes.string.isRequired,
    parameters: ImmutablePropTypes.contains({
      GUID: PropTypes.string.isRequired,
      expression: ImmutablePropTypes.contains({
        text: PropTypes.string.isRequired,
      }).isRequired,
      text: ImmutablePropTypes.contains({
        text: PropTypes.string.isRequired,
      }).isRequired,
      character: ImmutablePropTypes.contains({
        text: PropTypes.string.isRequired,
      }).isRequired,
      isRecording: PropTypes.bool,
      playRecording: PropTypes.bool,
    })
  }),
  mode: PropTypes.string.isRequired,
  onEditInstruction: PropTypes.func.isRequired,
  onHandleSelect: PropTypes.func.isRequired,
}



