import React from 'react'
import pure from 'recompose/pure'
import SvgIcon from 'material-ui/SvgIcon'

let ArrowDownIcon = (props) => (
  <SvgIcon {...props} viewBox="0 0 50 50" >
    <g>
      <polygon points="24.8,44.2 7.8,26 4.2,29.6 24.8,50 45.8,30 42.2,26.4" />
      <g>
        <polygon points="22.8,47 22,3 27,3 26.8,47" />
      </g>
    </g>
  </SvgIcon>
)
ArrowDownIcon = pure(ArrowDownIcon)
ArrowDownIcon.displayName = 'ArrowDownIcon'
ArrowDownIcon.muiName = 'SvgIcon'

export default ArrowDownIcon





