import { INSTRUCTIONS_KEY, ID_KEY } from './../manuscriptStateConstants'
import {
  groupInstructionsByScenes,
  flattenInstructions,
  markSelectedInstructionById
} from './../utils/manuscriptUtils'

export const removeInstruction = (manuscript, instructionId, index) => {
  const prevInstructionSet = flattenInstructions(manuscript.getIn(INSTRUCTIONS_KEY))
  const newInstructionSet = prevInstructionSet.delete(index)

  if (newInstructionSet.isEmpty()) {
    return manuscript
      .setIn(['manuscript', 'selectedId'], undefined)
      .setIn(['manuscript', 'currentSceneId'], undefined)
      .setIn(INSTRUCTIONS_KEY, newInstructionSet)
  } else {
    const newSelectionIndex = Math.min(index, newInstructionSet.count() - 1)
    const newSelectionId = newInstructionSet.get(newSelectionIndex).getIn(ID_KEY)
    const currentSceneId = manuscript.getIn(['manuscript', 'currentSceneId'])

    const scenes = markSelectedInstructionById(groupInstructionsByScenes(newInstructionSet), newSelectionId)

    return manuscript
      .setIn(['manuscript', 'selectedId'], newSelectionId)
      .setIn(['manuscript', 'currentSceneId'], currentSceneId)
      .setIn(INSTRUCTIONS_KEY, scenes)
  }
}



