import {
  OPEN_CHARACTER_RENAME,
} from '../characterListConstants'

export default function (id) {
  return (dispatch) => {
    return dispatch(
      {
        type: OPEN_CHARACTER_RENAME,
        payload: {id}
      }
    )
  }
}



