import { Map, fromJS } from 'immutable'
import {
  ADD_CHARACTER_SUCCESS,
  DELETE_CHARACTER,
  COPY_CHARACTER,
  FETCH_CHARACTERS_SUCCESS,
  RENAME_CHARACTER_SUCCESS,
  DELETE_CHARACTER_SUCCESS,
  UPDATE_CHARACTER_THUMBNAIL
} from '../characterListConstants'


export default function reducer(characters = Map({}), action) {
  const payload = action.payload
  switch(action.type) {
    case ADD_CHARACTER_SUCCESS:
      return addCharacterReducer(characters, action)

    case DELETE_CHARACTER:
      return deleteCharacterReducer(characters, payload.id)

    case COPY_CHARACTER:
      return copyCharacterReducer(characters, payload.id, payload.newId)

    case FETCH_CHARACTERS_SUCCESS:
      return fetchedCharacters(characters, payload)

    case RENAME_CHARACTER_SUCCESS:
      return renameCharacter(characters, payload)

    case DELETE_CHARACTER_SUCCESS:
      return deleteCharacter(characters, payload)

    case UPDATE_CHARACTER_THUMBNAIL:
      return updateCharacterThumbnailReducer(characters, payload)

    default:
      return characters
  }
}

function addCharacterReducer(state, action) {
  const {character} = action.payload
  return state.set(character.id, fromJS(character))
}

function deleteCharacterReducer(characters, id) {
  return characters.delete(id)
}

function copyCharacterReducer(characters, id, newId) {
  const character = characters.get(id)
  if (character !== undefined) {
    return characters.set(newId, character.set("id", newId))
  } else {
    return characters
  }
}

function fetchedCharacters(characters, payload) {
  return characters.merge(
    payload.characters.reduce((acc, character) => {
      return acc.set(character.id, fromJS(character))
    }, Map({}))
  )
}

function renameCharacter(characters, payload) {
  return characters.setIn([payload.id, 'name'], payload.newName)
}

function deleteCharacter(characters, payload) {
  return characters.delete(payload.id)
}

const updateCharacterThumbnailReducer = (state, payload) => {
  return state.setIn([payload.id, 'icon'], payload.icon)
}



