import {Map} from 'immutable'

import { INSTRUCTIONS_KEY } from './../manuscriptStateConstants'
import { INSTRUCTION_MODE_OPEN } from '../manuscriptEditorConstants'
import { markSelectedInstructionById } from '../utils/manuscriptUtils'
export const selectInstruction = (manuscript, instructionId) => {
  const scenes = manuscript.getIn(INSTRUCTIONS_KEY)

  return manuscript
    .setIn(['manuscript', 'selectedInstruction'], Map({id: instructionId, state: INSTRUCTION_MODE_OPEN}))
    .setIn(INSTRUCTIONS_KEY, markSelectedInstructionById(scenes, instructionId))
}

export const selectCurrentScene = (manuscript, sceneId) => {
  return manuscript.setIn(['manuscript', 'currentSceneId'], sceneId)
}





