import {
  FETCH_EXPRESSIONS_START,
  FETCH_EXPRESSIONS_SUCCESS,
  FETCH_EXPRESSIONS_ERROR,
} from '../characterEditorConstants'
import { showError } from '../../UI/uiReducer'

export default function fetchCharacterExpressions() {
  return (dispatch, getState, unity) => {
    dispatch({type:FETCH_EXPRESSIONS_START})
    return unity.callAsync('GetCharacterCreatorViewData', 'animation', 'male')
      .then(data => {
        return dispatch({
          type: FETCH_EXPRESSIONS_SUCCESS,
          payload: data
        })
      })
      .catch((error) => {
        dispatch({type: FETCH_EXPRESSIONS_ERROR})
        dispatch(showError('Failed to get character creator expressions', error))
      })
  }
}



