import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import shallowCompare from 'react-addons-shallow-compare'

import Dialog from 'material-ui/Dialog'
import FlatButton from 'material-ui/FlatButton'
import { deleteCharacter, closeCharacterDelete } from  '../actionCreators/characterListActionCreators'


export class CharacterDelete extends Component {

  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  // REACT LIFECYCLE
  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  constructor(props) {
    super(props)
    // this.state = {}
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  // HANDLERS
  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  handleInput = (event) => this.setState({name: event.target.value})

  handleDelete = () => this.props.deleteCharacter(this.props.characterId)

  handleClose = () => this.props.closeCharacterDelete()

  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  // RENDER
  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  render() {
    const actions = [
      <FlatButton
        key="cancelDelete"
        label="Cancel"
        secondary
        onTouchTap={this.handleClose}
      />,
      <FlatButton
        key="delete"
        label="Delete"
        primary
        onTouchTap={this.handleDelete}
      />
    ]

    return (
      <Dialog
        title="Delete Character"
        open={this.props.characterId !== ""}
        actions={actions}
        modal={false}
        overlayStyle={styles.overlay}
        onRequestClose={this.handleClose}
        maxHeight={500}
      />
    )
  }
}


const styles = {
  overlay: {
    backgroundColor: 'rgba(126, 126,126, 0.1)'
  }
}


CharacterDelete.propTypes = {
  characterId: PropTypes.string,
  deleteCharacter: PropTypes.func.isRequired,
  closeCharacterDelete: PropTypes.func.isRequired
}

function mapStateToProps(state) {
  return {
    characterId: state.getIn(['ui', 'characterList', 'characterDeleteId'], ""),
  }
}

export const CharacterDeleteContainer = connect(
  mapStateToProps,
  {deleteCharacter, closeCharacterDelete}
)(CharacterDelete)



