import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import { Tabs, Tab } from 'material-ui/Tabs'
import Paper from 'material-ui/Paper'
import { CharacterListContainer } from '../CharacterList/components/CharacterList'
import { ManuscriptListContainer } from '../ManuscriptList/components/ManuscriptList'


export default class ContentLists extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleChange = (value) => {
    this.context.router.push(`/${value}`)
  }

  removeSlashes = (str) => str.replace(/\//g, '')

  render() {
    const activePath = this.removeSlashes(this.props.location.pathname)
    const activeList = activePath === 'characters'
      ? <CharacterListContainer  style={styles.content} />
      : <ManuscriptListContainer style={styles.content} />

    return (
      <div style={styles.root} >
        {activeList}
        <Paper style={styles.header} zDepth={2}>
          <Tabs onChange={this.handleChange}
            value={activePath}
            style={styles.root}
            contentContainerStyle={styles.container}
          >
            <Tab label="Characters" value="characters" />
            <Tab label="Plots" value="plots" />
          </Tabs>
        </Paper>
      </div>
    )
  }
}

ContentLists.propTypes = {
  location: PropTypes.shape({
    pathname: PropTypes.string
  })
}
ContentLists.contextTypes = {
  router: PropTypes.object
}

const styles = {
  root: {
    width: '100%',
    height: '100%',
    background: 'white',
  },
  header: {
    display: 'block',
    position: 'fixed',
    width: '100%',
    top: 0,
    right: 0,
    left: 0
  },
  content: {
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100%'
    // width: '100%',
    // height: '600px',
    // background: 'white',
  },
}



