import { goBack } from 'react-router-redux'
import { showError } from '../../UI/uiReducer'
import { orphanInstructions } from '../utils/manuscriptUtils'
import { addSceneInstruction } from './addInstruction'
import { moveScene } from './moveInstruction'
import { selectFirstInstruction } from './selectInstruction'
import { createScene } from '../utils/instructionCreators'
import {
  LOAD_MANUSCRIPT_START,
  LOAD_MANUSCRIPT_SUCCESS
} from '../manuscriptEditorConstants'

function loadManuscriptStart(id) {
  return {
    type: LOAD_MANUSCRIPT_START,
    payload: { id }
  }
}

function loadManuscriptSuccess(id, manuscript, content) {
  return {
    type: LOAD_MANUSCRIPT_SUCCESS,
    payload: { id, manuscript, content }
  }
}

export function loadManuscript(id, fixedManuscript = false) {
  return (dispatch, getState, unity) => {
    dispatch(loadManuscriptStart(id))
    return unity.callAsync('OpenManuscript', id)
      .then(manuscript => {
        dispatch(loadManuscriptSuccess(id, manuscript, getState().get('content').toJS()))

        /**
         * Add an empty scene and move it to the first position. This scene
         * will be the parent for the orphan instructions.
         */
        const orphans = orphanInstructions(manuscript)
        if (!orphans.isEmpty() && !fixedManuscript) {
          const scene = createScene(orphans.toJS())
          addSceneInstruction(scene)(dispatch, getState, unity)
          moveScene(scene.getIn(['parameters', 'GUID']), -1)(dispatch, getState, unity)
          loadManuscript(id, true)(dispatch, getState, unity)
        } else {
          dispatch(selectFirstInstruction())
        }

        return manuscript
      })
      .catch((error) => {
        // eslint-disable-next-line
        console.log("Error while loading manuscript: ", error)
        dispatch(goBack())
        dispatch(showError('Failed to open plot', error))
      })
  }
}



