export const sceneInstruction = {
  font: '15px/1.2em CourierPrime, Courier, sans-serif',
  padding: '10px 10px 10px 34px'
}

export const subInstructionInnerStyle = {
  ...sceneInstruction,
  padding: '10px 10px 10px 57px'
}

export const subInstructionStyle = (expanded) => {
  return {
    backgroundColor: 'transparent',
    paddingBottom: expanded ? 40 : 0
  }
}

export const sceneInstructionIcon = {
  fill: '#fe6521',
  width: 17,
  height: 17,
  margin: 0,
  top: 10,
  left: 8
}

export const subInstructionIcon = {
  ...sceneInstructionIcon,
  top: 10,
  left: 33
}


