import {Map} from 'immutable'
import {
  FETCH_CHARACTER_ITEMS,
  FETCH_CHARACTER_ITEMS_SUCCESS,
  GOTO_CHARACTER_EDITOR_VIEW
} from '../characterEditorConstants'

export function gotoViewCategory(targetViewName) {
  if (targetViewName === '') return {type: 'NO_ACTION'}

  return (dispatch, getState, unity) => {
    return createViewDataTuple(getState, targetViewName)
      .then(callUnityChangeCharacterCreatorView(dispatch, unity, targetViewName))
      .then(callUnityGetCharacterCreatorViewData(dispatch, getState, unity))
      .then(dispatchResults(dispatch))
  }
}

const createViewDataTuple = (getState, targetViewName) => {
  const viewData = getState().get('characterEditorViewData')
  const sequence = viewData.toIndexedSeq()
    .filter((view) => view.get('displaytype') !== 'hidden')
  const index = sequence
    .reduce((reduction, value, key) => {
      if (value.get('name') === targetViewName) {
        return key
      }
      return reduction
    }, 0)
  const currentView = sequence.get(index)
  return Promise.resolve({currentView, sequence, index})
}

const callUnityChangeCharacterCreatorView = (dispatch, unity) => {
  return ({currentView, sequence, index}) => {
    const previousView = index - 1 < 0 ? Map() : sequence.get(index - 1, Map({}))
    const nextView = index + 1 === sequence.count() ? Map() : sequence.get(index + 1)
    unity.call("ChangeCharacterCreatorView", currentView.get('name'), false)

    dispatch({
      type: GOTO_CHARACTER_EDITOR_VIEW,
      payload: {previousView, currentView, nextView}
    })

    return Promise.resolve(currentView)
  }
}

const callUnityGetCharacterCreatorViewData = (dispatch, getState, unity) => {
  return (currentView) => {
    dispatch({type: FETCH_CHARACTER_ITEMS})

    const categories = currentView.get('categories')
    const displayType = currentView.get('displaytype')
    const gender = getState().getIn(['currentCharacter', 'gender'])
    const name = getState().getIn(['currentCharacter', 'name'])

    const contentPromises = categories.map((category) => {
      return Promise.all([
        Promise.resolve(category),
        unity.callAsync("GetCharacterCreatorViewData", category, gender, name)
      ])
    }).toArray()

    return { contentPromises, displayType }
  }
}

const dispatchResults = (dispatch) => {
  return ({contentPromises, displayType}) => {
    return Promise.all(contentPromises).then((value) => {
      const categories = value.map(tuple => {
        return {
          category: tuple[0],
          items: tuple[1].items
        }
      })

      dispatch({
        type: FETCH_CHARACTER_ITEMS_SUCCESS,
        payload: {
          categories,
          displayType
        }
      })
    })
  }
}



