import React, { PropTypes } from 'react'
import { fromJS } from 'immutable'
import shallowCompare from 'react-addons-shallow-compare'
import ImmutablePropTypes from 'react-immutable-proptypes'
import InstructionWrapper from '../InstructionWrapper'
import {
  updateInstruction,
  replaceCharacterInScene,
} from '../../actionCreators/updateInstruction'

import {
  selectInstructionById,
  selectCurrentSceneById,
} from '../../actionCreators/selectInstruction'

import {
  moveScene
} from '../../actionCreators/moveInstruction'

import { removeScene } from '../../actionCreators/removeInstruction'

import ValidationMessage from '../ValidationMessage'
import { InstructionControlsContainer } from '../InstructionControls'

import Dialog from 'material-ui/Dialog'
import FlatButton from 'material-ui/FlatButton'

import { ListItem } from 'material-ui/List'

import SceneIcon from '../SvgIcons/SceneIcon'

import * as styles from '../styles/instructionListStyles'

export default class SceneInstruction extends React.Component {

  constructor(props) {
    super(props)
    this.id = props.instruction.getIn(['parameters', 'GUID'])
    this.state = {
      confirmDialogueOpen: false,
      instructionControlsVisible: false
    }
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleSelect = () => {
    if (!this.props.instruction.get('selected')) {
      this.context.store.dispatch(selectInstructionById(this.id))
    }
  }

  handleSelectCurrentScene = () => {
    if (this.props.currentSceneId !== this.id) {
      this.context.store.dispatch(selectCurrentSceneById(this.id))
    }
  }

  handleSceneChange = (selection) => {
    // Only update if it actually changed
    if (selection.id !== this.props.instruction.getIn(['parameters', 'scene', 'id'])) {
      const scene = fromJS({id: selection.id, text: selection.name})
      const updated = this.props.instruction
        .setIn(['parameters', 'scene'], scene)
        .setIn(['parameters', 'location1'], undefined)
        .setIn(['parameters', 'location2'], undefined)

      this.context.store.dispatch(updateInstruction(this.id, updated))
    }
  }

  handleLocation1Change = (selection) => {

    if (selection.id !== this.props.instruction.getIn(['parameters', 'location1', 'id'])) {
      const location = fromJS({id: selection.id, text: selection.text})
      const updated = this.props.instruction
        .setIn(['parameters', 'location1'], location)

      this.context.store.dispatch(updateInstruction(this.id, updated))
    }
  }

  handleLocation2Change = (selection) => {
    if (selection.id !== this.props.instruction.getIn(['parameters', 'location2', 'id'])) {
      const location = fromJS({id: selection.id, text: selection.text})
      const updated = this.props.instruction
        .setIn(['parameters', 'location2'], location)

      this.context.store.dispatch(updateInstruction(this.id, updated))
    }
  }

  handleCharacter1Change = (selection) => {
    this.context.store.dispatch(replaceCharacterInScene(
      this.id,
      selection.id,
      this.props.instruction.getIn(['parameters', 'actor1', 'id']),
      'actor1'
    ))
  }

  handleCharacter2Change = (selection) => {
    this.context.store.dispatch(replaceCharacterInScene(
      this.id,
      selection.id,
      this.props.instruction.getIn(['parameters', 'actor2', 'id']),
      'actor2'
    ))
  }

  handleInstructionEdit =() => { console.log("handleInstructionEdit") }

  validationMessage = (instruction) => {
    const validationError = findFirstValidationError(
      instruction,
      ['scene', 'actor1', 'actor2', 'location1', 'location2']
    )
    const isSelected = instruction.get('selected')

    if (validationError && isSelected) {
      return (
        <ValidationMessage validationError={validationError} />
      )
    } else {
      return null
    }
  }

  handleRemove = (e) => {
    e.stopPropagation()
    if (!this.props.childInstructions.isEmpty()) {
      if (confirm(`The scene and it's ${this.props.childInstructions.count()} related instructions will be removed. Are you sure?`)) {
        this.context.store.dispatch(removeScene(this.id))
      }
    } else {
      this.context.store.dispatch(removeScene(this.id))
    }
  }

  handleMoveUp = (e) => {
    e.stopPropagation()
    this.context.store.dispatch(moveScene(this.id, -1))
  }

  handleMoveDown = (e) => {
    e.stopPropagation()
    this.context.store.dispatch(moveScene(this.id, 1))
  }

  handleOpenRemoveDialogue = () => this.setState({confirmDialogueOpen: true})

  handleCloseRemoveDialogue = () => this.setState({confirmDialogueOpen: false})

  handleInstructionControlsVisible = () => this.setState({instructionControlsVisible: !this.state.instructionControlsVisible})

  handleEdit = () => {}

  createConfirmRemoveDialogue = () => {

    const handleRemoveScene = () => {
      this.context.store.dispatch(removeScene(this.id))
    }

    const actions = [
      <FlatButton
        key="cancelRemove"
        label="Cancel"
        onTouchTap={this.handleCloseRemoveDialogue}
      />,
      <FlatButton
        key="remove"
        label="Remove"
        primary
        onTouchTap={handleRemoveScene}
      />
    ]

    return (
      <Dialog
        actions={actions}
        modal={false}
        open={this.state.confirmDialogueOpen}
        onRequestClose={this.handleCloseRemoveDialogue}
      >
        {`The scene and it's ${this.props.childInstructions.count()} related instructions will be removed. Are you sure?`}
      </Dialog>
    )
  }

  createChildInstructions = (charactersInScene) => (
    this.props.childInstructions
      .map((instruction, index) => {
        const GUID = instruction.getIn(['parameters', 'GUID'])
        return (
          <InstructionWrapper
            key={GUID}
            index={index}
            id={GUID}
            text={instruction.get('type') + " " + instruction.getIn(['parameters', 'GUID'])}
            type={instruction.get('type')}
            instruction={instruction}
            content={this.props.content}
            charactersInScene={charactersInScene}
            isFirstInstruction={false}
            isLastInstruction={false}
            editMode={this.props.editMode}
          />
        )
      })
      .toArray()
  )

  render() {
    const charactersInScene = this.props.instruction.get('charactersInScene')

    const parameters = this.props.instruction.get('parameters')

    const primary = (
      <div>
        <span style={{fontWeight: 'bold', textTransform: 'uppercase'}}>{parameters.getIn(['scene', 'text'])}</span>
        <div key={Math.random()}>
          <div>
            <span>{parameters.getIn(['actor1', 'text'])} is {parameters.getIn(['location1', 'text'])}</span>
          </div>
          <div>
            <span>{parameters.getIn(['actor2', 'text'])} is {parameters.getIn(['location2', 'text'])}</span>
          </div>
        </div>
        <InstructionControlsContainer
          id={this.props.id}
          isVisible={this.state.instructionControlsVisible}
          onEdit={this.handleEdit}
        />
      </div>
    )

    const childInstructions = this.createChildInstructions(charactersInScene)

    return (
      <div>
        <ListItem
          primaryText={primary}
          innerDivStyle={styles.sceneInstruction}
          leftIcon={<SceneIcon style={styles.sceneInstructionIcon} />}
          style={{backgroundColor: 'transparent'}}
        />
        {childInstructions}
    </div>
    )
  }
}

SceneInstruction.propTypes = {
  id: PropTypes.string,
  currentSceneId: PropTypes.string,
  instruction: ImmutablePropTypes.mapContains({
    selected: PropTypes.bool.isRequired,
    charactersInScene: ImmutablePropTypes.listOf(
      ImmutablePropTypes.mapContains({
        id: PropTypes.string,
        name: PropTypes.string
      })
    ).isRequired,
    parameters: ImmutablePropTypes.mapContains({
      GUID: PropTypes.string.isRequired,
      scene: ImmutablePropTypes.mapContains({
        id: PropTypes.string
      }),
      actor1: ImmutablePropTypes.mapContains({
        id: PropTypes.string
      }),
      actor2: ImmutablePropTypes.mapContains({
        id: PropTypes.string
      })
    })
  }),
  childInstructions: ImmutablePropTypes.list.isRequired,
  content: ImmutablePropTypes.contains({
    scenes: ImmutablePropTypes.list.isRequired,
    characters: ImmutablePropTypes.list.isRequired,
    actions: ImmutablePropTypes.list.isRequired,
    expressions: ImmutablePropTypes.list.isRequired,
    music: ImmutablePropTypes.list.isRequired,
    sounds: ImmutablePropTypes.list.isRequired
  }),
  isFirstInstruction: PropTypes.bool.isRequired,
  isLastInstruction: PropTypes.bool.isRequired,
  editMode: PropTypes.bool.isRequired
}
SceneInstruction.contextTypes = {
  store: React.PropTypes.object
}



