import {
  OPEN_MANUSCRIPT_RENAME
} from '../manuscriptListConstants'

export default function (id, name) {
  return (dispatch) => {
    return dispatch({
      type: OPEN_MANUSCRIPT_RENAME,
      payload: { id , name }
    })
  }
}



