import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'

export default class ValidationMessage extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  render() {
    return (
      <div className="error-message">
        <span className="absolute-icon icon-warning warning">
          {this.props.validationError}
        </span>
      </div>
    )
  }
}

ValidationMessage.propTypes = {
  validationError: PropTypes.string.isRequired
}


