import { combineReducers } from 'redux-immutable'
import manuscriptsReducer from '../modules/ManuscriptList/reducers/manuscriptListReducer'
import charactersReducer from '../modules/CharacterList/reducers/characterListReducer'
import characterEditorReducer from '../modules/CharacterEditor/reducers/characterEditorReducer'
import characterEditorViewDataReducer from '../modules/CharacterEditor/reducers/characterEditorViewDataReducer'
import characterExpressionReducer from '../modules/CharacterEditor/reducers/characterExpressionReducer'
import uiReducer from '../modules/UI/uiReducer'
import sessionReducer from '../modules/Session/session'
import editorReducer from '../modules/ManuscriptEditor/reducers/manuscriptEditorReducer'
import routerReducer from '../modules/ImmutableRoute/immutableRouterReducer'
import contentReducer from '../modules/Content/contentReducer'
import playerReducer from '../modules/ManuscriptEditor/reducers/playerReducer'

const rootReducer = combineReducers({
  manuscripts: manuscriptsReducer,
  currentManuscript: editorReducer,
  characters: charactersReducer,
  currentCharacter: characterEditorReducer,
  characterEditorViewData: characterEditorViewDataReducer,
  characterExpressions: characterExpressionReducer,
  appConfig: state => state,
  ui: uiReducer,
  session: sessionReducer,
  routing: routerReducer,
  content: contentReducer,
  player: playerReducer
})

export default rootReducer



