import React, {Component, PropTypes} from 'react'
import shallowCompare from 'react-addons-shallow-compare'

import { setGender } from '../../../actionCreators/characterEditorActionCreators'

export default class GenderPicker extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  onClick = (gender) => {
    return () => {
      this.context.store.dispatch(setGender(gender))
    }
  }
  render() {
    return (
      <div className="start">
        <div className="credentials">
          <div className="genders">
            <div className="gender male " onClick={this.onClick('male')}><span>Male</span></div>
            <div className="gender female" onClick={this.onClick('female')}><span >Female</span>
            </div>
          </div>
          <button className="randomize" onClick={this.onClick('random')} >Randomize</button>
        </div>
      </div>
    )
  }
}

GenderPicker.contextTypes = {
  store: PropTypes.object
}



