import {
  ID_KEY,
  INSTRUCTIONS_KEY
} from './../manuscriptStateConstants'

import { modifyInstructions } from './../utils/manuscriptUtils'


export const recordVoiceSuccess = (manuscript, dialogueId) => {
  const scenes = manuscript.getIn(INSTRUCTIONS_KEY)

  const updatedInstructions = modifyInstructions(scenes, (i) => {
    if (i.getIn(ID_KEY) === dialogueId) {
      return i
        .setIn(['parameters', 'playRecording'], true)
        .setIn(['parameters', 'isRecorded'], true)
    }
    return i
  })

  return manuscript.setIn(INSTRUCTIONS_KEY, updatedInstructions)
}



