import {
  List,
  fromJS
} from 'immutable'

import {
  LOAD_INITIAL_DATA_SUCCESS,
  SHOW_NOTIFICATION,
  NotificationStatus
} from './uiActionCreators'

import {
  GOTO_CHARACTER_EDITOR_VIEW,
  FETCH_CHARACTER_ITEMS_SUCCESS,
  CLOSE_CHARACTER_EDITOR_START,
  CLOSE_CHARACTER_EDITOR_SUCCESS,
  SELECT_CHARACTER_ITEM,
  OPEN_CHARACTER_PHOTO_SUCCESS,
  CLOSE_CHARACTER_PHOTO
} from '../CharacterEditor/characterEditorConstants'

import {
  OPEN_CHARACTER_RENAME,
  CLOSE_CHARACTER_RENAME,
  RENAME_CHARACTER_SUCCESS,
  OPEN_CHARACTER_DELETE,
  CLOSE_CHARACTER_DELETE,
  DELETE_CHARACTER_SUCCESS
} from '../CharacterList/characterListConstants'

import {
  OPEN_MANUSCRIPT_RENAME,
  CLOSE_MANUSCRIPT_RENAME,
  RENAME_MANUSCRIPT_SUCCESS,
  OPEN_MANUSCRIPT_DELETE,
  CLOSE_MANUSCRIPT_DELETE,
  DELETE_MANUSCRIPT_SUCCESS
} from '../ManuscriptList/manuscriptListConstants'

import { LOAD_MANUSCRIPT_SUCCESS, CLOSE_MANUSCRIPT_START } from '../ManuscriptEditor/manuscriptEditorConstants'

export const INITIAL_STATE = fromJS({
  isInitialDataLoaded: false,
  manuscriptEditor: {
    isReady: false
  },
  characterEditor: {
    isReady: false
  }
})

const defaultTimeout = 4000

export default function reducer(uiState = INITIAL_STATE, action) {
  switch (action.type) {
    case LOAD_INITIAL_DATA_SUCCESS:
      return uiState.set('isInitialDataLoaded', true)

    case GOTO_CHARACTER_EDITOR_VIEW:
      return gotoCharacterEditorView(uiState, action)

    case CLOSE_CHARACTER_EDITOR_START:
      return uiState.setIn(['characterEditor', 'isReady'], false)

    case CLOSE_CHARACTER_EDITOR_SUCCESS:
      return characterEditorClosed(uiState)

    case FETCH_CHARACTER_ITEMS_SUCCESS:
      return registerCharacterEditorItems(uiState, action)

    case SELECT_CHARACTER_ITEM:
      return selectCharacterItem(uiState, action)

    case LOAD_MANUSCRIPT_SUCCESS:
      return uiState.setIn(['manuscriptEditor', 'isReady'], true)

    case CLOSE_MANUSCRIPT_START:
      return uiState.setIn(['manuscriptEditor', 'isReady'], false)

    case OPEN_CHARACTER_PHOTO_SUCCESS:
      return insertCharacterPhoto(uiState, action)

    case CLOSE_CHARACTER_PHOTO:
      return removeCharacterPhoto(uiState)

    case OPEN_CHARACTER_RENAME:
      return openCharacterRename(uiState, action)

    case CLOSE_CHARACTER_RENAME:
      return closeCharacterRename(uiState)

    case RENAME_CHARACTER_SUCCESS:
      return closeCharacterRename(uiState)

    case OPEN_CHARACTER_DELETE:
      return openCharacterDelete(uiState, action)

    case CLOSE_CHARACTER_DELETE:
      return closeCharacterDelete(uiState)

    case DELETE_CHARACTER_SUCCESS:
      return closeCharacterDelete(uiState)

    case OPEN_MANUSCRIPT_RENAME:
      return openManuscriptRename(uiState, action)

    case CLOSE_MANUSCRIPT_RENAME:
      return closeManuscriptRename(uiState)

    case RENAME_MANUSCRIPT_SUCCESS:
      return closeManuscriptRename(uiState)

    case OPEN_MANUSCRIPT_DELETE:
      return openManuscriptDelete(uiState, action)

    case CLOSE_MANUSCRIPT_DELETE:
      return closeManuscriptDelete(uiState)

    case DELETE_MANUSCRIPT_SUCCESS:
      return closeManuscriptDelete(uiState)

    default:
      return uiState
  }
}

function showNotification(msg, status, timeout = defaultTimeout) {
  return {
    type: SHOW_NOTIFICATION,
    payload: {
      msg: msg,
      timeout: timeout,
      status: status
    }
  }
}

function gotoCharacterEditorView(state, action) {
  return state.setIn(['characterEditor', 'currentView'], fromJS(action.payload))
}

function selectCharacterItem(state, action) {
  const variations = action.payload.item.get('variations', List())
  return state.setIn(['characterEditor', 'variations'], variations)
}

function characterEditorClosed(state) {
  return state
    .delete('characterEditor')
    .set('characterEditor', fromJS({isReady: false}))
}

function registerCharacterEditorItems(state, action) {
  return state
    .setIn(['characterEditor', 'categories'], fromJS(action.payload.categories), List())
    .setIn(['characterEditor', 'displayType'], fromJS(action.payload.displayType), 'normal')
    .setIn(['characterEditor', 'isReady'], true)
}

function insertCharacterPhoto(state, action) {
  return state.setIn(['characterEditor', 'photo'], fromJS(
    {
      "thumb": action.payload.thumb,
      "path": action.payload.path
    }
  ))
}

function removeCharacterPhoto(state) {
  return state.deleteIn(['characterEditor', 'photo'])
}

function openCharacterRename(state, action) {
  return state.setIn(['characterList', 'characterRenameId'], action.payload.id)
}

function closeCharacterRename(state) {
  return state.deleteIn(['characterList', 'characterRenameId'])
}

function openCharacterDelete(state, action) {
  return state.setIn(['characterList', 'characterDeleteId'], action.payload.id)
}

function closeCharacterDelete(state) {
  return state.deleteIn(['characterList', 'characterDeleteId'])
}

// Move to manuscriptListReducer?
function openManuscriptRename(state, action) {
  return state.setIn(['manuscriptList', 'manuscriptRenameId'], action.payload.id)
}

function closeManuscriptRename(state) {
  return state.deleteIn(['manuscriptList', 'manuscriptRenameId'])
}

function openManuscriptDelete(state, action) {
  return state.setIn(['manuscriptList', 'manuscriptDeleteId'], action.payload.id)
}

function closeManuscriptDelete(state) {
  return state.deleteIn(['manuscriptList', 'manuscriptDeleteId'])
}

export function showError(msg, cause, overrideTimeout) {
  console.log(cause)
  return showNotification(msg, NotificationStatus.Error, overrideTimeout)
}

export function showSuccess(msg, overrideTimeout) {
  return showNotification(msg, NotificationStatus.Success, overrideTimeout)
}



