import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'

import TextField from 'material-ui/TextField'
import IconButton from 'material-ui/IconButton'
import HighlightOff from 'material-ui/svg-icons/action/highlight-off'

import { setName } from '../../../actionCreators/characterEditorActionCreators'

export default class NamePicker extends Component {
  constructor(props) {
    super(props)

    this.state = {
      showWipeTextField: false
    }
  }

  componentWillReceiveProps = (nextProps) => this.setState({name: nextProps.name, id: nextProps.id})
  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)
  handleSetName = (event) => {
    this.setState({
      name: event.target.value
    })
    return () => {
      this.context.store.dispatch(setName(this.props.id, event.target.value))
    }
  }

  handleShowWipeTextField = () => this.setState({ showWipeTextField: !this.state.showWipeTextField  })
  handleClearTextField = () => {
    this.setState({ name: '' })
  }

  wipeTextField = () => {
    return (
      <IconButton style={styles.zindex}
        onClick={this.handleClearTextField}
      >
        <HighlightOff />
      </IconButton>
    )
  }

  render() {
    return (
      <div style={styles.body}>
        <div style={styles.content}>
          <div style={styles.name}>
            <div style={styles.textfield}>
              <TextField
                name="character-name"
                value={this.state.name}
                floatingLabelText="Character Name"
                onChange={this.handleSetName}
                onFocus={this.handleShowWipeTextField}
                onBlur={this.handleShowWipeTextField}
              />
            </div>
            <div style={styles.wipeIcon}>
              {this.state.showWipeTextField ? this.wipeTextField() : null}
            </div>
            <button style={styles.button} onClick={this.props.onGoBack}>Done</button>
          </div>
        </div>
      </div>
    )
  }
}

const styles = {
  body: {
    height: '100%',
    width: '100%',
    display: 'flex',
    alignItems: 'center'
  },
  content: {
    margin: '0 auto',

  },
  name: {
    width: '250px',
  },
  zindex: {
    position: 'relative',
    zIndex: '1000'
  },
  textfield: {
    width: '200px',
    float: 'left',
    zIndex: '-1'
  },
  wipeIcon: {
    float: 'right',
    marginTop: '20px'
  },
  button: {
    marginTop: '20px',
    display: 'block',
    width: '200px',
    padding: '12px',
    marginLeft: 'auto',
    marginRight: 'auto',
    fontSize: '1em',
    backgroundColor: '#ffc12f'
  }
}

NamePicker.contextTypes = {
  store: PropTypes.object
}

NamePicker.propTypes = {
  id: PropTypes.string.isRequired,
  onGoBack: PropTypes.func.isRequired
}



