import { findInstructionById, findInstructionIndexById } from '../utils/manuscriptUtils'
import { REMOVE_INSTRUCTION } from '../manuscriptEditorConstants'

/**
 * Also removes all potential child instructions
 *
 * @param sceneId The scene to remove
 */
export function removeScene(sceneId) {
  return (dispatch, getState, unity) => {

    const scenes = getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    const scene = findInstructionById(sceneId, scenes)
    const index = findInstructionIndexById(sceneId, scenes)

    // Start by removing all child components
    scene.get('childInstructions').forEach(i => {
      removeInstruction(i.getIn(['parameters', 'GUID']))(dispatch, getState, unity)
    })

    // Lastly remove the parent scene
    unity.call('RemoveInstruction', index, sceneId)
    dispatch({
      type: REMOVE_INSTRUCTION,
      payload: {instructionId: sceneId, index}
    })
  }
}

/**
 * Remove instruction
 */
export function removeInstruction(instructionId) {
  return (dispatch, getState, unity) => {

    const instructions = getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    const index = findInstructionIndexById(instructionId, instructions)

    unity.call('RemoveInstruction', index, instructionId)
    dispatch({
      type: REMOVE_INSTRUCTION,
      payload: {instructionId, index}
    })
  }
}



