export const VALIDATION_ERROR_KEY = "validationError"

export const validateInstruction = (instruction, content, validForActions, charactersInScene) => {
  switch (instruction.get('type')) {
    case 'dialogue':
      return validateDialogue(instruction, content.get('characters'), content.get('expressions'))

    case 'action':
      return validateAction(
        instruction,
        validForActions,
        charactersInScene,
        content.get('actions')
      )

    case 'music':
      return validateMusic(instruction, content.get('music'))

    case 'sound':
      return validateSoundEffect(instruction, content.get('sounds'))

    default:
      return instruction
  }
}

export const validateScene = (sceneInstruction, content) => {
  const sceneParam = sceneInstruction.getIn(['parameters', 'scene'])
  const actor1Param = sceneInstruction.getIn(['parameters', 'actor1'])
  const actor2Param = sceneInstruction.getIn(['parameters', 'actor2'])
  const location1Param = sceneInstruction.getIn(['parameters', 'location1'])
  const location2Param = sceneInstruction.getIn(['parameters', 'location2'])

  // Not much point in validating further if a scene hasn't been picked
  if (sceneParam === undefined) {
    return sceneInstruction
  }

  const scene = content.get('scenes').find(s => s.get('id') === sceneParam.get('id'))

  // Not much point in validating further if scene doesn't exist
  if (scene === undefined) {
    return sceneInstruction
      .setIn(['parameters', 'scene'], sceneParam.set(VALIDATION_ERROR_KEY, 'Scene is not available'))
  }

  const allLocations = scene.get('locations')
  const validForActions = isValidForActions(location1Param, location2Param, allLocations)
  const children = sceneInstruction
    .get('childInstructions')
    .map(i => validateInstruction(i, content, validForActions, sceneInstruction.get('charactersInScene')))

  const jahapp = sceneInstruction
    .set('childInstructions', children)

  return sceneInstruction
    .set('validForActions', validForActions)
    .set('childInstructions', children)
    .setIn(['parameters', 'actor1'], validateCharacterParam(actor1Param, content.get('characters')))
    .setIn(['parameters', 'actor2'], validateCharacterParam(actor2Param, content.get('characters')))
    .setIn(['parameters', 'location1'], validateLocationParam(location1Param, allLocations))
    .setIn(['parameters', 'location2'], validateLocationParam(location2Param, allLocations))
}

export const validateDialogue = (dialogueInstruction, charactersInScene, expressions) => {
  const characterParam = dialogueInstruction.getIn(['parameters', 'character'])
  const expressionParam = dialogueInstruction.getIn(['parameters', 'expression'])
  return dialogueInstruction
    .setIn(['parameters', 'character'], validateCharacterParam(characterParam, charactersInScene))
    .setIn(['parameters', 'expression'], validateExpressionParam(expressionParam, expressions))
}

export const validateAction = (actionInstruction, closeEnoughForAction, charactersInScene, actions) => {
  const characterParam = actionInstruction.getIn(['parameters', 'character'])
  const targetParam = actionInstruction.getIn(['parameters', 'target'])
  const actionParam = actionInstruction.getIn(['parameters', 'action'])

  if (!closeEnoughForAction) {
    return actionInstruction
      .set('validForActions', closeEnoughForAction)
      .set(VALIDATION_ERROR_KEY, 'Characters are too far apart')
      .setIn(['parameters', 'character'], validateCharacterParam(characterParam, charactersInScene))
      .setIn(['parameters', 'target'], validateCharacterParam(targetParam, charactersInScene))
  } else {
    return actionInstruction
      .set('validForActions', closeEnoughForAction)
      .delete(VALIDATION_ERROR_KEY)
      .setIn(['parameters', 'character'], validateCharacterParam(characterParam, charactersInScene))
      .setIn(['parameters', 'target'], validateCharacterParam(targetParam, charactersInScene))
      .setIn(['parameters', 'action'], validateActionParam(actionParam, actions))
  }
}

export const validateMusic = (musicInstruction, music) => {
  const musicParam = musicInstruction.getIn(['parameters', 'music'])
  return musicInstruction
    .setIn(['parameters', 'music'], validateMusicParam(musicParam, music))
}

export const validateSoundEffect = (soundInstruction, sounds) => {
  const soundParam = soundInstruction.getIn(['parameters', 'sound'])
  return soundInstruction
    .setIn(['parameters', 'sound'], validateSoundEffectParam(soundParam, sounds))
}

const validateCharacterParam = (characterParam, allCharacters) => {
  if (characterParam === undefined) {
    return characterParam
  } else if (!allCharacters.some(c => c.get('id') === characterParam.get('id'))) {
    return characterParam.set(VALIDATION_ERROR_KEY, 'Character is not available')
  }
  return characterParam
}

const validateLocationParam = (locationParam, allLocations) => {
  if (locationParam === undefined) {
    return locationParam
  } else if (!allLocations.some(l => l.get('id') === locationParam.get('id'))) {
    return locationParam.set(VALIDATION_ERROR_KEY, 'Location is not available')
  }
  return locationParam
}

const validateExpressionParam = (expressionParam, allExpressions) => {
  if (expressionParam === undefined) {
    return expressionParam
  } else if (!allExpressions.some(l => l.get('id') === expressionParam.get('id'))) {
    return expressionParam.set(VALIDATION_ERROR_KEY, 'Expression is not available')
  }
  return expressionParam
}

const validateMusicParam = (musicParam, allMusic) => {
  if (musicParam === undefined) {
    return musicParam
  } else if (!allMusic.some(l => l.get('id') === musicParam.get('id'))) {
    return musicParam.set(VALIDATION_ERROR_KEY, 'Music is not available')
  }
  return musicParam
}

const validateSoundEffectParam = (soundEffectParam, allSoundEffects) => {
  if (soundEffectParam === undefined) {
    return soundEffectParam
  } else if (!allSoundEffects.some(l => l.get('id') === soundEffectParam.get('id'))) {
    return soundEffectParam.set(VALIDATION_ERROR_KEY, 'Sound effect is not available')
  }
  return soundEffectParam
}

const validateActionParam = (actionParam, allActions) => {
  if (actionParam === undefined) {
    return actionParam
  } else if (!allActions.some(l => l.get('id') === actionParam.get('id'))) {
    return actionParam.set(VALIDATION_ERROR_KEY, 'Action is not available')
  }
  return actionParam
}

/**
 * Determines wether or not this scene allows for actions.
 * Actions requires two actors that are next to each other.
 *
 * @param actor1Location  The location of actor1
 * @param actor2Location  The location of actor2
 * @param allLocations    All possible locations
 */
const isValidForActions = (actor1Location, actor2Location, allLocations) => {
  if (actor1Location === undefined || actor2Location === undefined) {
    return false
  } else {
    const location1 = allLocations.find(l => l.get('id') === actor1Location.get('id'))
    const location2 = allLocations.find(l => l.get('id') === actor2Location.get('id'))

    if (location1 === undefined || location2 === undefined) {
      return false
    } else if (location1.get('adjacent') === location2.get('id')) {
      return true
    } else {
      return false
    }
  }
}



