import {
  DELETE_CHARACTER_START,
  DELETE_CHARACTER_SUCCESS,
  DELETE_CHARACTER_ERROR,
} from '../characterListConstants'

export default function (id) {
  return (dispatch, getState, unity) => {
    dispatch({type: DELETE_CHARACTER_START})
    return unity.call('DeleteCharacter', id)
      .then(()=> {
        return dispatch(
          {
            type: DELETE_CHARACTER_SUCCESS,
            payload: {id}
          })
      })
      .catch((error) => {
        dispatch({type: DELETE_CHARACTER_ERROR})
        dispatch(showError('Failed to delete character', error))
      })
  }
}



