import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import {connect} from 'react-redux'
import { resizePreviewWindow } from '../../UI/uiActionCreators'
import { findDOMNode } from 'react-dom'
import StopIcon from 'material-ui/svg-icons/av/stop'
import PlayIcon from 'material-ui/svg-icons/av/play-arrow'
import IconButton from 'material-ui/IconButton'
import ClassNames from 'classnames'
import * as playerActionCreators from '../actionCreators/playerActionCreators'

export default class PreviewWindow extends Component {

  componentDidMount() {
    const previewWindowDOMNode = findDOMNode(this)
    this.context.store.dispatch(
      resizePreviewWindow(
        (previewWindowDOMNode.offsetLeft / window.innerWidth),
        (previewWindowDOMNode.offsetTop / window.innerHeight),
        (previewWindowDOMNode.offsetWidth / window.innerWidth),
        (previewWindowDOMNode.offsetHeight / window.innerHeight)
      )
    )
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handlePlayStop = () => {
    this.props.isPlaying ? this.props.stop() : this.props.play()
  }

  render() {
    const isPlaying = this.props.isPlaying
    const className = ClassNames({
      'preview-window': true,
      'transparent': this.props.isManuscriptReady
    })
    const icon = () => (isPlaying
      ? <StopIcon ref="stopIcon" />
      : <PlayIcon ref="playIcon" />
      )

    return (
      <div className={className} style={styles.root} ref="previewWindow">
        <IconButton
          className="playStop"
          style={styles.button}
          iconStyle={styles.bigIcon}
          onClick={this.handlePlayStop}
        >
          {icon()}
        </IconButton >
      </div>

    )
  }
}

const styles = {
  root: {
    height: 210,
    width: '100%',
    textAlign: "center"
  },

  button: {
    width:"120px",
    height: "120px",
    padding: 30,
    margin: "auto",
    display: "block",
    transform: "translate(0 , 50%)"
  },

  bigIcon: {
    color: "white",
    width: "60px",
    height: "60px",
  },
}

PreviewWindow.propTypes = {
  isPlaying: PropTypes.bool.isRequired,
  isManuscriptReady: PropTypes.bool.isRequired,
  play: PropTypes.func.isRequired,
  stop: PropTypes.func.isRequired,
}

PreviewWindow.contextTypes = {
  store: PropTypes.object
}

const mapStateToProps = (state) => {
  return {
    isPlaying: state.getIn(['player', 'isPlaying'], false),
    isManuscriptReady: state.getIn(['ui', 'manuscriptEditor', 'isReady'], false),
  }
}

export const PreviewWindowContainer = connect(
  mapStateToProps,
  playerActionCreators
)(PreviewWindow)



