import { createDialogue, createAction, createMusic, createScene, createSound } from '../utils/instructionCreators'
import { newSelection, selectCurrentSceneById } from './selectInstruction'
import { ADD_INSTRUCTION, ADD_SCENE_INSTRUCTION } from '../manuscriptEditorConstants'
import { ID_KEY } from '../manuscriptStateConstants'
import { stripVisualData } from '../utils/instructionUtils'

function instructionAction(instruction) {
  return {
    type: ADD_INSTRUCTION,
    payload: {
      instruction: instruction.toJS()
    },
    meta: {
      metrics: {
        name: 'Add Instruction',
        data: {
          type: instruction.type
        }
      }
    }
  }
}

export function addDialogueInstruction() {
  return (dispatch, getState, unity) => {
    const instruction = createDialogue()
    unity.call('AddInstruction', stripVisualData(instruction))
    dispatch(instructionAction(instruction))
    dispatch(newSelection(instruction.getIn(ID_KEY)))
  }
}

export function addActionInstruction() {
  return (dispatch, getState, unity) => {
    const instruction = createAction()
    unity.call('AddInstruction', stripVisualData(instruction))
    dispatch(instructionAction(instruction))
    dispatch(newSelection(instruction.getIn(ID_KEY)))
  }
}

export function addMusicInstruction() {
  return (dispatch, getState, unity) => {
    const instruction = createMusic()
    unity.call('AddInstruction', stripVisualData(instruction))
    dispatch(instructionAction(instruction))
    dispatch(newSelection(instruction.getIn(ID_KEY)))
  }
}

export function addSoundEffectInstruction() {
  return (dispatch, getState, unity) => {
    const instruction = createSound()
    unity.call('AddInstruction', stripVisualData(instruction))
    dispatch(instructionAction(instruction))
    dispatch(newSelection(instruction.getIn(ID_KEY)))
  }
}

export function addSceneInstruction(newScene = undefined) {
  return (dispatch, getState, unity) => {
    const scene = newScene || createScene()
    unity.call('AddInstruction', stripVisualData(scene))
    dispatch({
      type: ADD_SCENE_INSTRUCTION,
      payload: { scene: scene.toJS() },
      meta: {
        metrics: {
          name: 'Add Instruction',
          data: {
            type: 'scene'
          }
        }
      }
    })

    dispatch(newSelection(scene.getIn(ID_KEY)))
    dispatch(selectCurrentSceneById(scene.getIn(ID_KEY)))
  }
}



