import { replace } from 'react-router-redux'
import { showError } from '../../UI/uiReducer'
import { CLOSE_MANUSCRIPT_START, CLOSE_MANUSCRIPT_SUCCESS } from '../manuscriptEditorConstants'

export function closeManuscript() {
  return (dispatch, getState, unity) => {
    dispatch({type: CLOSE_MANUSCRIPT_START})
    return unity.callAsync('CloseManuscript')
      .then(() => {
        return dispatch({type: CLOSE_MANUSCRIPT_SUCCESS})
      })
      .catch((error) => {
        return dispatch(showError('Failed to close plot', error))
      })
      .then(() => {
        return dispatch(replace('/plots'))
      })
  }
}



