import React, { Component, PropTypes } from 'react'
import ImmutablePropTypes from 'react-immutable-proptypes'
import shallowCompare from 'react-addons-shallow-compare'

import { ListItem } from 'material-ui/List'

import MusicIcon from '../SvgIcons/MusicIcon'

import * as constants from '../../manuscriptEditorConstants'
import * as styles from '../styles/instructionListStyles'

import { InstructionControlsContainer } from '../InstructionControls'

export default class MusicInstruction extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  _createContentNodes = (mode) => {
    const instruction = this.props.instruction
    const GUID = instruction.getIn(['parameters', 'GUID'])

    switch (mode) {
      case constants.INSTRUCTION_MODE_EDITING:
        return <h1 id={GUID} >{text}</h1>

      default:
        return (
          <div>
            <span>{instruction.getIn(['parameters', 'music', 'text'])} starts playing</span>
            <InstructionControlsContainer id={GUID}
              isVisible={this.props.mode === constants.INSTRUCTION_MODE_OPEN}
              onEdit={this.props.onEditInstruction}
            />
          </div>
        )
    }
  }

  render() {
    const instruction = this.props.instruction
    const GUID = instruction.getIn(['parameters', 'GUID'])
    const content = this._createContentNodes(this.props.mode)
    return (
      <ListItem
        key={GUID}
        className="music-instruction-container"
        primaryText={content}
        leftIcon={<MusicIcon style={styles.subInstructionIcon} />}
        style={styles.subInstructionStyle(this.props.mode === constants.INSTRUCTION_MODE_OPEN)}
        innerDivStyle={styles.subInstructionInnerStyle}
        onTouchTap={this.props.onHandleSelect}
      />
    )
  }
}

MusicInstruction.propTypes = {
  instruction: ImmutablePropTypes.mapContains({
    parameters: ImmutablePropTypes.mapContains({
      GUID: PropTypes.string.isRequired,
      music: ImmutablePropTypes.mapContains({
        id: PropTypes.string.isRequired,
        text: PropTypes.string.isRequired
      })
    })
  }),
  mode: PropTypes.string.isRequired,
  onEditInstruction: PropTypes.func.isRequired,
  onHandleSelect: PropTypes.func.isRequired,
}
MusicInstruction.contextTypes = {
  store: PropTypes.object
}



