import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import ImmutablePropTypes from 'react-immutable-proptypes'

import {Map} from 'immutable'

import {connect} from 'react-redux'

import * as actionCreators from '../actionCreators/manuscriptEditorActionCreators'
import { findFirstValidationError } from '../utils/manuscriptUtils'
import ValidationMessage from './ValidationMessage'

import DialogueInstruction from './instructions/DialogueInstruction'
import MusicInstruction from './instructions/MusicInstruction'
import SoundEffectInstruction from './instructions/SoundEffectInstruction'
import ActionInstruction from './instructions/ActionInstruction'

import * as constants from '../manuscriptEditorConstants'

export class InstructionWrapper extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleSelect = (e) => {
    e.stopPropagation()
    const GUID = this.props.instruction.getIn(['parameters', 'GUID'])
    this.props.selectInstructionById(GUID)
  }

  handleEdit = () => {
    const GUID = this.props.instruction.getIn(['parameters', 'GUID'])
    this.props.editInstruction(GUID)
  }

  validationMessage = (instruction) => {
    const validationError = findFirstValidationError(
      instruction,
      ['action', 'character', 'target', 'expression', 'music', 'sound']
    )
    const isSelected = instruction.get('selected')

    if (validationError && isSelected) {
      return (
        <ValidationMessage validationError={validationError} />
      )
    } else {
      return null
    }
  }

  createComponent = () => {
    const instruction = this.props.instruction
    const type = instruction.get('type')
    const GUID = instruction.getIn(['parameters', 'GUID'])

    const props = {
      key: GUID,
      instruction,
      mode: this.props.mode,
      onEditInstruction: this.handleEdit,
      onHandleSelect: this.handleSelect
    }

    switch (type) {
      case 'music':
        return <MusicInstruction {...props} />

      case 'dialogue':
        return <DialogueInstruction {...props} />

      case 'sound':
        return <SoundEffectInstruction {...props} />

      case 'action':
        return <ActionInstruction {...props} />
    }
  }

  render() {
    return this.createComponent()
  }
}

InstructionWrapper.propTypes = {
  index: PropTypes.number.isRequired,
  id: PropTypes.string.isRequired,
  instruction: ImmutablePropTypes.mapContains({
    parameters: ImmutablePropTypes.mapContains({
      GUID: PropTypes.string.isRequired
    })
  }).isRequired,
  selectInstructionById: PropTypes.func.isRequired,
  editInstruction: PropTypes.func.isRequired,
  mode: PropTypes.string.isRequired
}

const mapStateToProps = (state, ownProps) => {
  const id = ownProps.id
  const selectedInstruction = state.getIn(constants.SELECTED_INSTRUCTION_KEY, Map({id:""}))
  return {
    mode: selectedInstruction.get('id') === id ? selectedInstruction.get('state') : constants.INSTRUCTION_MODE_CLOSED
  }
}
export default connect(
  mapStateToProps,
  actionCreators
)(InstructionWrapper)



