import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import SceneInstruction from './instructions/SceneInstruction'
import ImmutablePropTypes from 'react-immutable-proptypes'

import { List } from 'material-ui/List'

export default class SceneList extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  render() {
    const { sceneInstructions } = this.props

    const sceneInstructionComponents = sceneInstructions.map((i, index) => {
      const isFirstInstruction = index === 0
      const isLastInstruction = index === sceneInstructions.count() - 1
      const id = i.getIn(['parameters', 'GUID'])

      return (
        <SceneInstruction
          key={id}
          id={id}
          instruction={i}
          currentSceneId={this.props.currentSceneId}
          childInstructions={i.get('childInstructions')}
          content={this.props.content}
          isFirstInstruction={isFirstInstruction}
          isLastInstruction={isLastInstruction}
          editMode={this.props.editMode}
        />
      )
    })

    return (
      <List style={styles.sceneList} className="always-scrollable">
        <div style={this.props.playerState ? styles.overlay : {}} >
          {sceneInstructionComponents}
        </div>
      </List>
    )
  }
}

const styles = {
  sceneList: {
    overflowY: 'scroll',
    flex: 1,
    backgroundColor: 'white',
    paddingTop: 4,
    paddingBottom: 4
  },
  overlay: {
    position: 'absolute',
    height: '100%',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, .85)',
    zIndex: '100',
    overflowY: 'hidden'
  }
}

SceneList.propTypes = {
  currentSceneId: PropTypes.string,
  sceneInstructions: ImmutablePropTypes.list.isRequired,
  content: ImmutablePropTypes.contains({
    scenes: ImmutablePropTypes.list.isRequired,
    characters: ImmutablePropTypes.list.isRequired,
    actions: ImmutablePropTypes.list.isRequired,
    expressions: ImmutablePropTypes.list.isRequired,
    music: ImmutablePropTypes.list.isRequired,
    sounds: ImmutablePropTypes.list.isRequired
  }),
  editMode: PropTypes.bool,
  playerState: PropTypes.string
}



