import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'

import IconButton from 'material-ui/IconButton'
import FileDownload from 'material-ui/svg-icons/file/file-download'
import KeyboardArrowLeft from 'material-ui/svg-icons/hardware/keyboard-arrow-left'

import { closeManuscript } from '../actionCreators/closeManuscript'
import { shareManuscript } from '../actionCreators/shareManuscript'

export default class Share extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleCloseManuscript = () => this.context.store.dispatch(closeManuscript())
  handleShare = () => this.context.store.dispatch(shareManuscript())

  render() {
    return (
      <div style={styles.container}>
        <div>
          <IconButton onClick={this.handleCloseManuscript} disabled={this.props.playerState ? true : false}>
            <KeyboardArrowLeft /> {/* TODO: Add color="white" later */}
          </IconButton>
        </div>
        <div>
          <IconButton onClick={this.handleShare} disabled={this.props.playerState ? true : false}>
            <FileDownload /> {/* TODO:  Add color="white" later */}
          </IconButton>
        </div>
      </div>
    )
  }

}

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'nowrap',
    justifyContent: 'space-between'
  }
}

Share.propTypes = {
  playerState: PropTypes.string
}

Share.contextTypes = {
  store: PropTypes.object
}



