import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import shallowCompare from 'react-addons-shallow-compare'

import Dialog from 'material-ui/Dialog'
import FlatButton from 'material-ui/FlatButton'
import { deleteManuscript, closeManuscriptDelete } from '../actionCreators/manuscriptListActionCreators'

export class ManuscriptDelete extends Component {
  constructor(props) {
    super(props)
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleInput = (event) => this.setState({name: event.target.value})
  handleDelete = () => this.props.deleteManuscript(this.props.manuscriptId)
  handleClose = () => this.props.closeManuscriptDelete()

  render() {
    const actions = [
      <FlatButton
        key="cancelDelete"
        label="Cancel"
        secondary
        onTouchTap={this.handleClose}
      />,
      <FlatButton
        key="delete"
        label="Delete"
        primary
        onTouchTap={this.handleDelete}
      />
    ]

    return (
      <Dialog
        title="Delete Manuscript"
        open={this.props.manuscriptId !== ""}
        actions={actions}
        modal={false}
        overlayStyle={styles.overlay}
        onRequestClose={this.handleClose}
        maxHeight={500}
      />
    )
  }
}

const styles = {
  overlay: {
    backgroundColor: 'rgba(126, 126, 126, .1)'
  }
}

ManuscriptDelete.propTypes = {
  manuscriptId: PropTypes.string,
  deleteManuscript: PropTypes.func.isRequired,
  closeManuscriptDelete: PropTypes.func.isRequired
}

function mapStateToProps(state) {
  return {
    manuscriptId: state.getIn(['ui', 'manuscriptList', 'manuscriptDeleteId'], "")
  }
}

export const ManuscriptDeleteContainer = connect(
  mapStateToProps,
  { deleteManuscript, closeManuscriptDelete }
)(ManuscriptDelete)



