import { UPDATE_CHARACTER_THUMBNAIL } from '../characterListConstants'

export const updateCharacterThumbnail = (id, icon) => {
  return {
    type: UPDATE_CHARACTER_THUMBNAIL,
    payload: {
      id,
      icon
    }
  }
}



