export const LOAD_CONTENT_START = "LOAD_CONTENT_START"
export const LOAD_CONTENT_SUCCESS = "LOAD_CONTENT_SUCCESS"
export const LOAD_CONTENT_ERROR = "LOAD_CONTENT_ERROR"
export function loadContent() {
  return (dispatch, getState, unity) => {
    dispatch({type:LOAD_CONTENT_START})
    return unity.callAsync('GetContent')
      .then(content => {
        return dispatch({
          type: LOAD_CONTENT_SUCCESS,
          payload: {content}
        })
      })
      .catch((error) => {
        dispatch({type:LOAD_CONTENT_ERROR})
        dispatch(showError('Failed to load content', error))
      })
  }
}



