import { goBack } from 'react-router-redux'
import { showError } from '../../UI/uiReducer'
import {
  LOAD_CHARACTER_START,
  LOAD_CHARACTER_SUCCESS
} from '../characterEditorConstants'

import {gotoViewCategory} from './gotoViewCategory'

export function loadCharacter(id, status) {
  return (dispatch, getState, unity) => {

    let localCharacter= findCharacterById(id, getState)
    if (!localCharacter) {
      return null
    }

    dispatch({
      type: LOAD_CHARACTER_START,
      payload: {id}
    })

    const targetViewCategory = status === 'new' ? 'Gender' : 'Head'

    return callUnityShowCharacterCreatorWindow(unity)(id)
      .then( callUnityLoadCharacter(unity, localCharacter.toJS())   )
      .then( callUnityGetCharacterCreatorViewData(unity, dispatch)  )
      .then( () => {
        return gotoViewCategory(targetViewCategory)(dispatch, getState, unity)
      })
      .catch((error) => {
        dispatch(goBack())
        dispatch(showError('Failed to open character', error))
      })
  }
}

const findCharacterById = (characterId, getState) => {
  return getState()
    .getIn(['characters'])
    .find(c => c.get('id') === characterId)
}

const callUnityShowCharacterCreatorWindow = (unity) => {
  return (id) => {
    return unity.callAsync('ShowCharacterCreatorWindow', id)
  }
}
const callUnityLoadCharacter = (unity, character) => {
  return () => {
    return unity.callAsync('LoadCharacter', character)
  }
}
const callUnityGetCharacterCreatorViewData = (unity, dispatch) => {
  return (character) => {
    return dispatch({
      type: LOAD_CHARACTER_SUCCESS,
      payload: {
        id: character.id,
        character
      }
    })
  }
}



