import {
  INSTRUCTIONS_KEY
} from './../manuscriptStateConstants'

import {
  groupInstructionsByScenes,
  findInstructionById,
  flattenInstructions
} from './../utils/manuscriptUtils'


export const moveScene = (state, sceneId, sceneIndex, direction) => {
  const scenes = state.getIn(INSTRUCTIONS_KEY)
  const scene = findInstructionById(sceneId, scenes)

  const updatedScenes = scenes
    .delete(sceneIndex)
    .insert(sceneIndex + direction, scene)

  return state.setIn(INSTRUCTIONS_KEY, updatedScenes)
}

export const moveInstruction = (state, instructionId, index, direction) => {
  const scenes = state.getIn(INSTRUCTIONS_KEY)
  const instructions = flattenInstructions(scenes)
  const instructionToBeMoved = findInstructionById(instructionId, scenes)
  const targetIndex = index + direction

  const updatedInstructions = instructions
    .delete(index)
    .insert(targetIndex, instructionToBeMoved)

  return state.setIn(INSTRUCTIONS_KEY, groupInstructionsByScenes(updatedInstructions))
}



