import React, { Component } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import ImmutablePropTypes from 'react-immutable-proptypes'
import {Variations} from './Variations'

export default class StackedItemPicker extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  createContent = () => {
    return categories
      .map(category => {
        const categoryName = category.get('category')

        const variations = category
          .get('items')
          .map((item) => item.get('variations'))
          .first()

        return (
          <Variations key={categoryName}
            variations={variations}
            category={categoryName}
          />
        )
      })
  }

  render() {
    return (<div className="only-variations has-variations picker">
      {createContent(this.props.categories)}
    </div>)
  }
}

StackedItemPicker.propTypes = {
  categories: ImmutablePropTypes.list.isRequired
}



