import { showError } from '../../UI/uiReducer'
import {
  SET_CHARACTER_EXPRESSION_START,
  SET_CHARACTER_EXPRESSION_SUCCESS,
  SET_CHARACTER_EXPRESSION_ERROR
} from '../characterEditorConstants'

export function setCharacterExpression(id) {
  return (dispatch, getState, unity) => {

    dispatch({type: SET_CHARACTER_EXPRESSION_START})

    return unity.callAsync("SetCharacterAnimation", id)
      .then((status) => {
        dispatch({
          type: SET_CHARACTER_EXPRESSION_SUCCESS,
          payload: {id, status}
        })
      })
      .catch((error) => {
        dispatch({type: SET_CHARACTER_EXPRESSION_ERROR})
        dispatch(showError('Failed to select character expression', error))
      })
  }
}



