import React, {Component, PropTypes} from 'react'
import ImmutablePropTypes from 'react-immutable-proptypes'

import shallowCompare from 'react-addons-shallow-compare'

import IconMenu from 'material-ui/IconMenu'
import MenuItem from 'material-ui/MenuItem'
import IconButton from 'material-ui/IconButton'
import InsertEmoticonAsset from 'material-ui/svg-icons/editor/insert-emoticon'


export default class ExpressionsSelect extends Component {

  constructor(props) {
    super(props)

    this.handleTouchTap = this.handleTouchTap.bind(this)

    this.state = {
      valueSingle: '3',
      valueMultiple: ['3', '5']
    }
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleOnRequestChange(value) {
    this.setState({
      openMenu: value
    })
  }

  handleTouchTap (event, child) {
    this.props.onChange(child.key)
  }

  render () {
    const expressions = this.props.expressions.toJS()

    const menuItems = expressions.map(expression => <MenuItem key={expression.id} value={expression.id} primaryText={expression.name} />)

    const iconButton = (
      <IconButton iconStyle={styles.mediumIcon}
        style={styles.medium}
      >
        <InsertEmoticonAsset color="white" />
      </IconButton>
    )

    return (
      <IconMenu
        maxHeight={500}
        iconButtonElement={iconButton}
        open={this.state.openMenu}
        onItemTouchTap={this.handleTouchTap}
        onRequestChange={this.handleOnRequestChange}
      >
        {menuItems}
      </IconMenu>
    )
  }
}

ExpressionsSelect.propTypes = {
  expressions: ImmutablePropTypes.list.isRequired,
  onChange: PropTypes.func.isRequired
}
const styles = {
  medium: {
    width: 48,
    height: 48,
    padding: 0
  },
  mediumIcon: {
    width: 36,
    height: 36
  },
}



