import React, {Component, PropTypes} from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import classNames from 'classnames'

import {setItemVariation} from '../../../actionCreators/characterEditorActionCreators'

export default class Variation extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleVariationClick = () => {
    const {id, category} = this.props
    this.context.store.dispatch(setItemVariation(id, category))
  }

  render() {
    const cn = classNames({
      variation: true,
      selected: this.props.selected
    })

    return (<img src={this.props.url} className={cn} onClick={this.handleVariationClick} />)
  }
}

Variation.propTypes = {
  url: PropTypes.string.isRequired,
  category: PropTypes.string.isRequired,
  id: PropTypes.string.isRequired,
  selected: React.PropTypes.bool
}
Variation.contextTypes = {
  store: PropTypes.object
}



