import { List, fromJS } from 'immutable'
import { ID_KEY } from '../manuscriptStateConstants'

export function findScenes(instructions) {
  return instructions
    .filter(i => i.get('type') === 'scene')
}

export function findSceneChildInstructions(instructions, sceneInstructionId) {
  return instructions
    .skipUntil(i => i.getIn(ID_KEY) === sceneInstructionId)
    .rest()
    .takeWhile(i => i.get('type') !== 'scene')
}

/**
 * A tree structure where the root nodes are scene instructions that have child instructions
 *
 * @param instructions  A flat list of instructions
 */
export function groupInstructionsByScenes(instructions) {
  return findScenes(instructions).map(scene => {
    const childInstructions = findSceneChildInstructions(instructions, scene.getIn(ID_KEY))
    return scene.set('childInstructions', childInstructions)
  })
}

/**
 * The ui prefers to group the instructions in a tree structure. Unity want's it in a flat array.

 * @param scenes  The scenes and their child instructions
 */
export function flattenInstructions(scenes) {
  return scenes
    .map(s => {
      if (s.get('type') === 'scene') {
        return List([s]).concat(s.get('childInstructions'))
      } else {
        return List([s])
      }
    })
    .reduce((prev, next) => prev.concat(next))
}

export function findInstructionByIndex(index, getState) {
  const scenes = getState()
    .getIn(['currentManuscript', 'manuscript', 'instructions'])
  const flatInstructions = flattenInstructions(scenes)
  return flatInstructions.get(index)
}

export function findInstructionIndexById(instructionId, scenes) {
  return flattenInstructions(scenes)
    .findIndex(instruction => instruction.getIn(ID_KEY) === instructionId)
}

export function findInstructionById(instructionId, scenes) {
  return flattenInstructions(scenes)
    .find(i => i.getIn(ID_KEY) === instructionId)
}

export function findCharactersInScene(characterIds, allCharacters) {
  const characters = []
  characterIds.forEach(id => {
    const found = allCharacters.find(c => c.get('id') === id)
    if (found) characters.push(found)
  })
  return List(characters)
}

export function findFirstValidationError(instruction, paramKeys) {

  if (instruction.get('validationError')) return instruction.get('validationError')

  let validationError = undefined
  paramKeys.some(key => {
    const error = instruction.getIn(['parameters', key, 'validationError'])
    if (error) validationError = error
    return error !== undefined
  })
  return validationError
}

export function findCurrentScene(scenes, currentSceneId) {
  const selectedInstruction = findInstructionById(currentSceneId, scenes)
  if (selectedInstruction.get('type') === 'scene') {
    return selectedInstruction
  } else {
    return findInstructionById(currentSceneId)
  }
}

export function findInstructionIndex(instructions, id) {
  if (instructions.isEmpty()) {
    return 0
  } else {
    return instructions.findIndex(i => i.getIn(ID_KEY) === id)
  }
}

/**
 * We need the first instruction to be a scene. Otherwise we will have to add an empty scene.
 *
 * @param manuscript  The manuscript we got from Unity
 * @returns {boolean} If the manuscript is valid or not
 */
export const orphanInstructions = (manuscript) => {
  return fromJS(manuscript)
    .getIn(['instructions'])
    .takeUntil(i => i.get('type') === 'scene')
}

/**
 * Apply a function to all instructions including the parent scenes
 *
 * @param scenes
 * @param updater
 * @returns {List}
 */
export const modifyInstructions = (scenes, updater) => {
  return scenes.map(scene => {
    const updatedChildInstructions = scene.get('childInstructions').map(updater)
    return updater(scene)
      .set('childInstructions', updatedChildInstructions)
  })
}

// Add a field that's more convenient to extract the actors from
export const saveCharactersInScene = (instruction, allCharacters) => {
  const charactersInScene = []
  if (instruction.getIn(['parameters', 'actor1']) !== undefined) {
    const actor1 = allCharacters.find(c => c.get('id') === instruction.getIn(['parameters', 'actor1', 'id']))
    if (actor1) {
      charactersInScene.push(actor1)
    }
  }
  if (instruction.getIn(['parameters', 'actor2']) !== undefined) {
    const actor2 = allCharacters.find(c => c.get('id') === instruction.getIn(['parameters', 'actor2', 'id']))
    if (actor2) {
      charactersInScene.push(actor2)
    }
  }

  return instruction.set('charactersInScene', fromJS(charactersInScene))
}

export const markSelectedInstructionById = (scenes, selectedId) => {
  return modifyInstructions(scenes, (i) => {
    return i.set('selected', i.getIn(ID_KEY) === selectedId)
  })
}



