import { Map, fromJS, List } from 'immutable'
import { LOAD_CONTENT_SUCCESS } from './contentActionCreators'

const initialState = fromJS({
  scenes: [],
  characters: [],
  actions: [],
  expressions: [],
  music: [],
  sounds: []
})

export default function reducer(content = initialState, action) {

  switch(action.type) {
    case LOAD_CONTENT_SUCCESS:
      return loadedContent(action.payload.content)

    default:
      return content
  }
}

const loadedContent = (content) => {

  const newContent = fromJS(content)

  // Sort alphabetically by name
  return Map({
    scenes: newContent.get('scenes', List([])).sort((a, b) => a.get('name').localeCompare(b.get('name'))),
    characters: newContent.get('characters', List([])).sort((a, b) => a.get('name').localeCompare(b.get('name'))),
    actions: newContent.get('actions', List([])).sort((a, b) => a.get('name').localeCompare(b.get('name'))),
    expressions: newContent.get('expressions', List([])).sort((a, b) => a.get('name').localeCompare(b.get('name'))),
    music: newContent.get('music', List([])).sort((a, b) => a.get('name').localeCompare(b.get('name'))),
    sounds: newContent.get('sounds', List([])).sort((a, b) => a.get('name').localeCompare(b.get('name')))
  })
}



