export const SHOW_NOTIFICATION = "SHOW_NOTIFICATION"
export const NotificationStatus = {
  Error: 'error',
  Success: 'success'
}


export const LOAD_INITIAL_DATA_SUCCESS = "LOAD_INITIAL_DATA_SUCCESS"
export const initialDataLoaded = {type: LOAD_INITIAL_DATA_SUCCESS}

export function resizePreviewWindow(x, y, width, height) {
  return (dispatch, getState, unity) => {
    return unity.call('ResizePreviewWindow', x, y, width, height)
      .catch(() => {
        dispatch(showError('Failed resizing preview window'))
      })
  }
}



