import { showError } from '../../UI/uiReducer'
import {
  SHARE_CHARACTER_PHOTO_START,
  SHARE_CHARACTER_PHOTO_SUCCESS,
  SHARE_CHARACTER_PHOTO_ERROR,
  GRAB_PREVIEW_START
} from '../characterEditorConstants'

export function shareCharacterPhoto(photoPath) {
  let width = 650
  let height = 960

  return (dispatch, getState, unity) => {
    dispatch({type: SHARE_CHARACTER_PHOTO_START})

    return unity.callAsync('GrabPreviewFrame', width, height)
      .then(() => {
        return dispatch({type: GRAB_PREVIEW_START})

      })
      .then((data) => {
        dispatch({
          type: SHARE_CHARACTER_PHOTO_SUCCESS,
          payload: data
        })
        return unity.callAsync('BeginShare', photoPath)
      })
      .catch((error) => {
        dispatch({type: SHARE_CHARACTER_PHOTO_ERROR})
        dispatch(showError('Failed to share character photo', error))
      })
  }
}



