import React, { PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'

import {Toolbar, ToolbarGroup} from 'material-ui/Toolbar'
import IconButton from 'material-ui/IconButton'

import SceneIcon from './SvgIcons/SceneIcon'
import DialogueIcon from './SvgIcons/DialogueIcon'
import ActionIcon from './SvgIcons/ActionIcon'
import SoundIcon from './SvgIcons/SoundIcon'
import MusicIcon from './SvgIcons/MusicIcon'

import { closeManuscript } from '../actionCreators/closeManuscript'
import * as styles from './styles/toolBarStyles'


import {
  addSceneInstruction,
  addDialogueInstruction,
  addActionInstruction,
  addSoundEffectInstruction,
  addMusicInstruction
} from '../actionCreators/addInstruction'


export default class ToolBar extends React.Component {

  constructor(props) {
    super(props)
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleGoBack = () => this.context.store.dispatch(closeManuscript())
  handleAddScene = () => this.context.store.dispatch(addSceneInstruction())
  handleAddDialogue = () => this.context.store.dispatch(addDialogueInstruction())
  handleAddAction = () => this.context.store.dispatch(addActionInstruction())
  handleAddSoundEffect = () => this.context.store.dispatch(addSoundEffectInstruction())
  handleAddMusic = () => this.context.store.dispatch(addMusicInstruction())
  handleEditMode = () => console.log("handleEditMode")

  render() {
    return (
      <Toolbar style={styles.toolBar}>
        <ToolbarGroup style={styles.toolBarGroup}>
          <IconButton onClick={this.handleAddScene}>
            <SceneIcon color="white" />
          </IconButton>
          <IconButton onClick={this.handleAddDialogue}>
            <DialogueIcon color="white" />
          </IconButton>
          <IconButton onClick={this.handleAddAction}>
            <ActionIcon color="white" />
          </IconButton>
          <IconButton onClick={this.handleAddSoundEffect}>
            <SoundIcon color="white" />
          </IconButton>
          <IconButton onClick={this.handleAddMusic}>
            <MusicIcon color="white" />
          </IconButton>

        </ToolbarGroup>
      </Toolbar>
    )
  }
}

ToolBar.propTypes = {
  hasSceneInstruction: PropTypes.bool
}
ToolBar.contextTypes = {
  store: PropTypes.object
}



