import { Map } from 'immutable'
import { VALIDATION_ERROR_KEY } from './validation'

/**
 * We need separate the "visual data" from the actual instruction data better. This is sort of a hack
 * before sending the instruction to unity
 *
 * @param instruction
 * @returns {*}
 */
export const stripVisualData = (instruction) => {

  let instructionMap
  if (Map.isMap(instruction)) {
    instructionMap = instruction.toJS()
  } else {
    instructionMap = instruction
  }

  if (instructionMap.validForActions) delete instructionMap.validForActions
  if (instructionMap.childInstructions) delete instructionMap.childInstructions
  if (instructionMap.charactersInScene) delete instructionMap.charactersInScene
  if (instructionMap.parameters.scene) delete instructionMap.parameters.scene[VALIDATION_ERROR_KEY]
  if (instructionMap.parameters.actor1) delete instructionMap.parameters.actor1[VALIDATION_ERROR_KEY]
  if (instructionMap.parameters.actor2) delete instructionMap.parameters.actor2[VALIDATION_ERROR_KEY]
  if (instructionMap.parameters.location1) delete instructionMap.parameters.location1[VALIDATION_ERROR_KEY]
  if (instructionMap.parameters.location2) delete instructionMap.parameters.location2[VALIDATION_ERROR_KEY]
  if (instructionMap.parameters.target) delete instructionMap.parameters.target[VALIDATION_ERROR_KEY]
  if (instructionMap.parameters.music) delete instructionMap.parameters.music[VALIDATION_ERROR_KEY]
  if (instructionMap.parameters.expression) delete instructionMap.parameters.expression[VALIDATION_ERROR_KEY]
  if (instructionMap.parameters.action) delete instructionMap.parameters.action[VALIDATION_ERROR_KEY]
  if (instructionMap.parameters.sound) delete instructionMap.parameters.sound[VALIDATION_ERROR_KEY]

  return instructionMap
}



