import {
  OPEN_MANUSCRIPT_DELETE
} from '../manuscriptListConstants'

export default function (id) {
  return (dispatch) => {
    return dispatch({
      type: OPEN_MANUSCRIPT_DELETE,
      payload: { id }
    })
  }
}



