import {
  fromJS,
  Map
} from 'immutable'

import {
  ADD_SCENE_INSTRUCTION,
  ADD_INSTRUCTION,
  SELECT_INSTRUCTION,
  MOVE_INSTRUCTION,
  MOVE_SCENE,
  UPDATE_INSTRUCTION,
  SELECT_CURRENT_SCENE,
  REMOVE_INSTRUCTION,
  LOAD_MANUSCRIPT_SUCCESS,
  CLOSE_MANUSCRIPT_SUCCESS,
  RECORD_VOICE_SUCCESS,
  TOGGLE_INSTRUCTION_CONTROLS,
  EDIT_INSTRUCTION,
  RECORD_MANUSCRIPT_START,
  SHARE_MANUSCRIPT_SUCCESS
} from './../manuscriptEditorConstants'

import { saveCharactersInScene } from './../utils/manuscriptUtils'

import { selectCurrentScene, selectInstruction } from './selectInstructionReducer'
import { addInstruction, addSceneInstruction } from './addInstructionReducer'
import { loadManuscript } from './loadManuscriptReducer'
import { moveInstruction, moveScene } from './moveInstructionReducer'
import { updateInstruction, updateScene } from './updateInstructionReducer'
import { removeInstruction } from './removeInstructionReducer'
import { recordVoiceSuccess } from './recordVoiceReducer'
import { toggleInstructionControls } from './toggleEditModeReducer'
import editInstructionReducer from './editInstructionReducer'


export default function (currentManuscript = Map({}), action) {
  switch (action.type) {

    case LOAD_MANUSCRIPT_SUCCESS:
      return loadManuscript(action.payload)

    case CLOSE_MANUSCRIPT_SUCCESS:
      return Map({})

    case ADD_SCENE_INSTRUCTION:
      const {scene} = action.payload
      return addSceneInstruction(currentManuscript, fromJS(scene))

    case ADD_INSTRUCTION:
      const {instruction} = action.payload
      return addInstruction(currentManuscript, fromJS(instruction))

    case SELECT_INSTRUCTION:
      return selectInstruction(currentManuscript, action.payload.instructionId)

    case SELECT_CURRENT_SCENE:
      return selectCurrentScene(currentManuscript, action.payload.sceneId)

    case MOVE_INSTRUCTION:
      return moveInstruction(
        currentManuscript,
        action.payload.instructionId,
        action.payload.index,
        action.payload.direction
      )

    case MOVE_SCENE:
      return moveScene(
        currentManuscript,
        action.payload.sceneId,
        action.payload.sceneIndex,
        action.payload.direction
      )

    case REMOVE_INSTRUCTION:
      const {instructionId, index} = action.payload
      return removeInstruction(currentManuscript, instructionId, index)

    case UPDATE_INSTRUCTION:
      if (action.payload.instruction.type === 'scene') {
        const updatedScene = saveCharactersInScene(fromJS(action.payload.instruction), fromJS(action.payload.content.characters))
        return updateScene(currentManuscript, updatedScene, action.payload.instructionId, fromJS(action.payload.content))
      } else {
        return updateInstruction(currentManuscript, fromJS(action.payload.instruction), action.payload.instructionId, fromJS(action.payload.content))
      }

    case RECORD_VOICE_SUCCESS:
      return recordVoiceSuccess(currentManuscript, action.payload.dialogueId)

    case TOGGLE_INSTRUCTION_CONTROLS:
      return toggleInstructionControls(currentManuscript)

    case EDIT_INSTRUCTION:
      return editInstructionReducer(currentManuscript, action)

    case RECORD_MANUSCRIPT_START:
      return currentManuscript.set('playerState', 'recording')

    case SHARE_MANUSCRIPT_SUCCESS:
      return currentManuscript.delete('playerState')

    default:
      return currentManuscript
  }
}



