import {
  INSTRUCTION_MODE_EDITING
} from '../manuscriptEditorConstants'

import { fromJS } from 'immutable'

export default (state, action) => {
  return state.setIn(['manuscript', 'selectedInstruction'], fromJS({ id: action.payload.ID, state: INSTRUCTION_MODE_EDITING}))
}



