import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import shallowCompare from 'react-addons-shallow-compare'
import { Link } from 'react-router'
import Paper from 'material-ui/Paper'
import FlatButton from 'material-ui/FlatButton'

import IconMenu from 'material-ui/IconMenu'
import MenuItem from 'material-ui/MenuItem'
import IconButton from 'material-ui/IconButton/IconButton'
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert'

import { openManuscriptRename, openManuscriptDelete } from '../actionCreators/manuscriptListActionCreators'

export default class ManuscriptListItem extends Component {
  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleRename = () => this.props.openManuscriptRename(this.props.id, this.props.name)

  handleDelete = () => this.props.openManuscriptDelete(this.props.id)

  render() {
    return (
      <Link to={`/plots/${this.props.id}`} style={styles.link} >
        <Paper style={styles.paper}>
          <img src={this.props.thumbnail} style={styles.img} />
          <div style={styles.buttonContent}>
            <FlatButton style={styles.flatButton}
              label={this.props.name}
            />
            <IconMenu id="actionMenu"
              iconButtonElement={<IconButton><MoreVertIcon color="black" /></IconButton>}
              anchorOrigin={{horizontal: 'right', vertical: 'top'}}
              targetOrigin={{horizontal: 'right', vertical: 'top'}}
            >
              <MenuItem id="renameButton" primaryText="Rename" onClick={this.handleRename} />
              <MenuItem id="deleteButton" primaryText="Delete" onClick={this.handleDelete}  />
            </IconMenu>
          </div>
        </Paper>
      </Link>
    )
  }
}

ManuscriptListItem.propTypes = {
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  thumbnail: PropTypes.string,
  openManuscriptRename: React.PropTypes.func.isRequired,
  openManuscriptDelete: React.PropTypes.func.isRequired
}

function mapStateToProps() {
  return {}
}

export const ManuscriptListItemContainer = connect(
  mapStateToProps,
  {openManuscriptRename, openManuscriptDelete}
)(ManuscriptListItem)


const styles = {
  link: {
    width: '100%',
    padding: 5,
    marginBottom: 15
  },
  paper: {
    display: 'flex',
    flexDirection: 'column'
  },
  img: {
    width: '100%',
    borderRadius: '2px 2px 0 0'
  },
  flatButton: {
    textAlign: 'left',
    height: 49,
    width: '100%'
  },
  buttonContent: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center'
  }
}



