import { SHOW_SUBTITLES, PREVIEW_PLOT_START, PREVIEW_PLOT_STOP } from '../manuscriptEditorConstants'

export function showSubtitles() {
  return (dispatch, getState, unity) => {
    unity.call('SetSubtitle')
    dispatch({
      type: SHOW_SUBTITLES
    })
  }
}

export function play() {
  return (dispatch, getState, unity) => {
    unity.call('PlayCurrentManuscript')
    dispatch({
      type: PREVIEW_PLOT_START,
      meta: {
        metrics: {
          name: 'Preview Plot'
        }
      }
    })
  }
}

export function manuscriptStopped() {
  return {
    type: PREVIEW_PLOT_STOP
  }
}

export function stop() {
  return (dispatch, getState, unity) => {
    unity.call('StopCurrentManuscript')
    dispatch(manuscriptStopped())
  }
}



