import {
  Map,
  fromJS
} from 'immutable'

import {
  SELECT_CHARACTER_ITEM,
  LOAD_CHARACTER_SUCCESS,
  SET_ITEM_VARIATION,
  CLOSE_CHARACTER_EDITOR_SUCCESS
} from '../characterEditorConstants'



export default function reducer (currentCharacter = Map({}), action) {
  switch(action.type) {

    case LOAD_CHARACTER_SUCCESS:
      return setCurrentCharacterData(currentCharacter, action)

    case SELECT_CHARACTER_ITEM:
      return selectCharacterItem(currentCharacter, action)

    case SET_ITEM_VARIATION:
      return setItemVariation(currentCharacter, action)

    case CLOSE_CHARACTER_EDITOR_SUCCESS:
      return Map()

    default:
      return currentCharacter
  }
}

const setCurrentCharacterData = (state, action) => {
  return fromJS(action.payload.character)
}

const selectCharacterItem = (state, action) => {
  const { category, id, selectedVariation } = action.payload
  return state
    .updateIn(['definition', category], (v) => v.set('id', id).set('variation', selectedVariation))
}

const setItemVariation = (state, action) => {
  const { category, id } = action.payload

  /*
   Because skincolor is set for the whole character but chosen as variation on heads.
   this is how we must do
   */
  if(action.payload.category === 'head')
    state = state.setIn(['definition', 'skincolor', 'variation'], id)

  return state
    .updateIn(['definition', category], (v) => v.set('variation', id))
}



