import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import shallowCompare from 'react-addons-shallow-compare'
import { Map } from 'immutable'

import Dialog from 'material-ui/Dialog'
import FlatButton from 'material-ui/FlatButton'

import {shareCharacterPhoto, closeCharacterPhoto} from  '../../actionCreators/characterEditorActionCreators'


export class CharacterPhoto extends Component {

  constructor(props) {
    super(props)
    this.state = {}
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleClose = () => this.props.closeCharacterPhoto()

  handleShare = () => this.props.shareCharacterPhoto(this.props.photoPath)

  render() {

    const actions = (
      <FlatButton
        label="Share"
        primary
        onTouchTap={this.handleShare}
      />
    )

    return (
      <Dialog
        bodyStyle={styles.dialogue}
        open={this.props.photoIsPresent}
        onRequestClose={this.handleClose}
        actions={actions}
      >
        <img style={styles.img}
          src={this.props.photoThumb}
        />
      </Dialog>
    )
  }
}


const styles = {
  dialogue: {
    padding: '8px 8px 0 8px'
  },
  medium: {
    width: 48,
    height: 48,
    padding: 0
  },
  mediumIcon: {
    width: 36,
    height: 36
  },
  img: {
    width: '100%',
    verticalAlign: 'bottom'
  }
}


CharacterPhoto.propTypes = {
  photoIsPresent: PropTypes.bool.isRequired,
  photoThumb: PropTypes.string,
  photoPath: PropTypes.string,
  shareCharacterPhoto: PropTypes.func.isRequired,
  closeCharacterPhoto: PropTypes.func.isRequired
}

function mapStateToProps(state) {
  return {
    photoIsPresent: !state.getIn(['ui', 'characterEditor', 'photo'], Map()).isEmpty(),
    photoThumb: state.getIn(['ui', 'characterEditor', 'photo', 'thumb']),
    photoPath: state.getIn(['ui', 'characterEditor', 'photo', 'path'])
  }
}

export const CharacterPhotoContainer = connect(
  mapStateToProps,
  {shareCharacterPhoto, closeCharacterPhoto}
)(CharacterPhoto)



