import React from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import {connect} from 'react-redux'
import ImmutablePropTypes from 'react-immutable-proptypes'

import FloatingActionButton from 'material-ui/FloatingActionButton'
import ContentAdd from 'material-ui/svg-icons/content/add'

import * as characterActionCreators from '../actionCreators/characterListActionCreators'
import {CharacterListItemContainer} from './CharacterListItem'
import { CharacterRenameContainer } from './CharacterRename'
import { CharacterDeleteContainer } from './CharacterDelete'


export class CharacterList extends React.Component{

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  createCharacters = (characters) => {
    return characters
      .toList()
      .map((character) => (
        <CharacterListItemContainer key={character.get('id')}
          name={character.get('name')}
          id={character.get('id')}
          image={character.get('icon')}
        />
      ))
  }

  handleClick = () => {
    this.props.addCharacter("Another name")
  }

  render() {
    return (
      <div style={styles.root}>
        <div style={styles.gridList} >
          {this.createCharacters(this.props.characters)}
        </div>
        <FloatingActionButton onClick={this.handleClick} style={styles.actionButton} secondary >
          <ContentAdd />
        </FloatingActionButton>
        <CharacterRenameContainer />
        <CharacterDeleteContainer />
      </div>
    )
  }
}

CharacterList.propTypes = {
  characters: ImmutablePropTypes.map.isRequired,
  addCharacter: React.PropTypes.func.isRequired,
}

function mapStateToProps(state) {
  return {
    characters: state.get('characters')
  }
}

export const CharacterListContainer = connect(
  mapStateToProps,
  characterActionCreators
)(CharacterList)

const styles = {
  root: {
    height: '100%'
  },
  gridList: {
    display: 'flex',
    flexWrap: 'wrap',
    width: '100%',
    height: '100%',
    overflowY: 'auto',
    paddingTop: 56
  },
  actionButton: {
    position: 'fixed',
    bottom: 20,
    right: 20
  }
}



