import {
  RECORD_MANUSCRIPT_START,
  RECORD_MANUSCRIPT_SUCCESS,
  SHARE_MANUSCRIPT_START,
  SHARE_MANUSCRIPT_SUCCESS
} from '../manuscriptEditorConstants'
import { showError } from '../../UI/uiReducer'

export function shareManuscript() {
  return (dispatch, getState, unity) => {
    dispatch({type: RECORD_MANUSCRIPT_START})
    return unity.callAsync('StartRecording')
      .then((path) => {
        dispatch({type: RECORD_MANUSCRIPT_SUCCESS})
        return dispatch({type: SHARE_MANUSCRIPT_START, payload: path})
      })
      .then((payload) => {
        dispatch({type: SHARE_MANUSCRIPT_SUCCESS})
        return unity.callAsync('SaveRecording', payload.path)
      })
      .catch((error) => {
        dispatch(showError('Failed to save plot to disk: ', error))

      })
  }
}



