import {
  DELETE_MANUSCRIPT_START,
  DELETE_MANUSCRIPT_SUCCESS,
  DELETE_MANUSCRIPT_ERROR
} from '../manuscriptListConstants'

export default function (id) {
  return (dispatch, getState, unity) => {
    dispatch({type: DELETE_MANUSCRIPT_START})
    return unity.callAsync('RemoveManuscript', id)
      .then(() => {
        return dispatch({
          type: DELETE_MANUSCRIPT_SUCCESS,
          payload: { id}
        })
      }).catch((error) => {
        return dispatch({
          type: DELETE_MANUSCRIPT_ERROR,
          payload: { error }
        })
      })
  }
}



