import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import ImmutablePropTypes from 'react-immutable-proptypes'
import {List, Map} from 'immutable'

import StackedItemPicker from './StackedItemPicker'
import NormalItemPicker from './NormalItemPicker'
import GenderPicker from './GenderPicker'
import NamePicker from './NamePicker'

export class PickerPane extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  createItemPicker() {
    switch(this.props.displayType) {
      case 'normal':
        return this.createNormalItemPicker()
      case 'stacked':
        return this.createStackedlItemPicker()
      case 'gender':
        return this.createGenderItemPicker()
      case 'name':
        return this.createNameItemPicker()
    }
  }

  createNormalItemPicker(){
    const {currentCharacter, categories} = this.props

    if (categories.size === 0) {
      return null
    }

    const category = categories.first()
    const categoryName = category.get('category')

    const currentSelection = currentCharacter.get('definition', List())
      .find(
        (value, key) => key === categoryName,
        null,
        Map({
          id: '',
          variation: ''
        })
      )

    return (
      <NormalItemPicker category={category}
        variations={this.props.variations}
        currentSelection={currentSelection ? currentSelection.toJS() : null}
      />
    )
  }
  createStackedlItemPicker(){
    if (categories.size === 0) {
      return null
    }
    return <StackedItemPicker categories={this.props.categories} />
  }
  createGenderItemPicker(){
    return <GenderPicker />
  }

  createNameItemPicker() {
    return (
      <NamePicker
        id={this.props.currentCharacter.get('id')}
        name={this.props.currentCharacter.get('name')}
        onGoBack={this.props.onGoBack}
      />
    )
  }

  render () {
    const { displayType} = this.props
    const itemPicker = this.createItemPicker()
    if (!itemPicker) {
      return null
    }

    const className = `pickers ${displayType}`
    return (
      <div className={className}>
        {itemPicker}
      </div>
    )
  }
}

PickerPane.propTypes = {
  currentCharacter: ImmutablePropTypes.map,
  categories: ImmutablePropTypes.list.isRequired,
  variations: ImmutablePropTypes.list.isRequired,
  displayType: PropTypes.string.isRequired,
  onGoBack: PropTypes.func.isRequired
}



