import {
  FETCH_CHARACTER_CREATOR_DATA_START,
  FETCH_CHARACTER_CREATOR_DATA_SUCCESS,
  FETCH_CHARACTER_CREATOR_DATA_ERROR
} from '../characterEditorConstants'
import { showError } from '../../UI/uiReducer'

export default function fetchCharacterCreatorData() {
  return (dispatch, getState, unity) => {
    dispatch({type:FETCH_CHARACTER_CREATOR_DATA_START})
    return unity.callAsync('GetCharacterCreatorData')
      .then(data => {
        return dispatch({
          type: FETCH_CHARACTER_CREATOR_DATA_SUCCESS,
          payload: data
        })
      })
      .catch((error) => {
        dispatch({type: FETCH_CHARACTER_CREATOR_DATA_ERROR})
        dispatch(showError('Failed to get character creator data'), error)
      })
  }
}



