import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import shallowCompare from 'react-addons-shallow-compare'

import Dialog from 'material-ui/Dialog'
import TextField from 'material-ui/TextField'
import FlatButton from 'material-ui/FlatButton'
import { renameManuscript, closeManuscriptRename } from '../actionCreators/manuscriptListActionCreators'

export class ManuscriptRename extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  componentWillReceiveProps = (nextProps) => this.setState({name: nextProps.manuscriptName})
  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleInput = (event) => this.setState({name: event.target.value});
  handleRename = () => this.props.renameManuscript(this.props.manuscriptId, this.state.name)
  handleClose = () => this.props.closeManuscriptRename()

  render() {
    const actions = [
      <FlatButton
        key="cancelRename"
        label="Cancel"
        secondary
        onTouchTap={this.handleClose}
      />,
      <FlatButton
        key="rename"
        label="Rename"
        primary
        onTouchTap={this.handleRename}
      />
    ]

    return (
      <Dialog
        title="Rename Manuscript/Plot"
        open={this.props.manuscriptId !== ""}
        actions={actions}
        modal={false}
        overlayStyle={styles.overlay}
        onRequestClose={this.handleClose}
        maxHeight={500}
      >
        <TextField id={this.props.manuscriptId} value={this.state.name} onChange={this.handleInput}
          ref="name"
        />
      </Dialog>
    )
  }
}

const styles = {
  overlay: {
    backgroundColor: 'rgba(126, 126,126, 0.1)'
  }
}

ManuscriptRename.propTypes = {
  manuscriptId: PropTypes.string,
  manuscriptName: PropTypes.string,
  renameManuscript: PropTypes.func.isRequired,
  closeManuscriptRename: PropTypes.func.isRequired
}

function mapStateToProps(state) {
  const manuscriptRenameId = state.getIn(['ui', 'manuscriptList', 'manuscriptRenameId'], "")
  return {
    manuscriptId: manuscriptRenameId,
    manuscriptName: state.getIn(['manuscripts', manuscriptRenameId, 'name'], "")
  }
}

export const ManuscriptRenameContainer = connect(
  mapStateToProps,
  {renameManuscript, closeManuscriptRename}
)(ManuscriptRename)



