import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { removeInstruction } from '../actionCreators/removeInstruction'
import { moveInstruction } from '../actionCreators/moveInstruction'
import IconButton from 'material-ui/IconButton'
import ArrowUpIcon from './SvgIcons/ArrowUpIcon'
import ArrowDownIcon from './SvgIcons/ArrowDownIcon'
import EditIcon from './SvgIcons/EditIcon'
import TrashIcon from './SvgIcons/TrashIcon'

export class InstructionControls extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleEdit = (e) => {
    e.stopPropagation()
    this.props.onEdit()
  }

  handleMoveUp = (e) => {
    e.stopPropagation()
    this.props.moveInstruction(this.props.id, -1)
  }

  handleMoveDown = (e) => {
    e.stopPropagation()
    this.props.moveInstruction(this.props.id, 1)
  }

  handleRemove = (e) => {
    e.stopPropagation()
    this.props.removeInstruction(this.props.id)
  }

  render() {
    return (
      <div style={styles.controls(this.props.isVisible)}>
        <IconButton className='edit-button' onTouchTap={this.handleEdit}>
          <EditIcon />
        </IconButton>
        <IconButton onTouchTap={this.handleMoveUp}>
          <ArrowUpIcon />
        </IconButton>
        <IconButton onTouchTap={this.handleMoveDown}>
          <ArrowDownIcon />
        </IconButton>
        <IconButton onTouchTap={this.handleRemove}>
          <TrashIcon />
        </IconButton>
      </div>
    )
  }
}

const styles = {
  controls(isVisible) {
    return {
      position: 'absolute',
      left: 0,
      width: '100%',
      display: 'flex',
      justifyContent: 'center',
      transition: 'all',
      transitionDuration: isVisible ? '200ms' : '100ms',
      transitionTimingFunction: isVisible ? 'ease-in' : 'ease-out',
      opacity: isVisible ? 1 : 0,
      bottom: isVisible ? -40 : -32,
      pointerEvents: isVisible ? 'auto' : 'none'
    }
  }
}

InstructionControls.propTypes = {
  id: PropTypes.string.isRequired,
  isVisible: PropTypes.bool.isRequired,
  onEdit: PropTypes.func.isRequired,
  moveInstruction: PropTypes.func.isRequired,
  removeInstruction: PropTypes.func.isRequired
}

function mapDispatchToProps(dispatch) {
  const actionCreators = Object.assign({ moveInstruction, removeInstruction })
  const boundActionCreators = bindActionCreators(actionCreators, dispatch)
  return Object.assign(boundActionCreators, {dispatch})
}

export const InstructionControlsContainer = connect(
  null,
  mapDispatchToProps
)(InstructionControls)



