// eslint-disable-next-line
import css from './styles/characterEditor.scss'

import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import { findDOMNode } from 'react-dom'
import { connect } from 'react-redux'
import ImmutablePropTypes from 'react-immutable-proptypes'
import { bindActionCreators } from 'redux'
import { Map } from 'immutable'

import * as characterActionCreators from '../../CharacterList/actionCreators/characterListActionCreators'
import * as characterEditorActionCreators from '../actionCreators/characterEditorActionCreators'
import * as uiActionCreators from '../../UI/uiActionCreators'

import { PreviewPaneContainer } from './PreviewPane/PreviewPane'
import { CharacterControlPane } from './ControlPane/CharacterControlPane'

export class CharacterEditor extends Component {

  componentWillMount() {
    const {loadCharacter, params} = this.props
    const status = this.props.location.query.status || 'existing'
    loadCharacter(params.characterId, status)
  }

  componentDidMount() {
    const previewPaneDOM = findDOMNode(this.refs.previewPane)
    if (previewPaneDOM) {
      this.positionPreviewWindow(previewPaneDOM)
    }
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  positionPreviewWindow(dom) {
    this.props.resizePreviewWindow(
      (dom.offsetLeft / window.innerWidth),
      (dom.offsetTop / window.innerHeight),
      (dom.offsetWidth / window.innerWidth),
      (dom.offsetHeight / window.innerHeight)
    )
  }

  render() {

    let panel
    if (this.props.uiReady) {
      panel = (
        <CharacterControlPane ref="characterControlPane" editorUIData={this.props.editorUIData}
          gotoViewCategory={this.props.gotoViewCategory}
          currentCharacter={this.props.currentCharacter}
          handleGoBack={this.props.closeCharacterEditor}
        />
      )
    }

    return (
      <div className="character-creator">
        <PreviewPaneContainer handleGoBack={this.props.closeCharacterEditor} ref="previewPane" />
        {panel}
      </div>
    )
  }
}

CharacterEditor.propTypes = {
  loadCharacter: PropTypes.func.isRequired,
  closeCharacterEditor: PropTypes.func.isRequired,
  resizePreviewWindow: PropTypes.func.isRequired,
  params: PropTypes.shape({
    characterId: PropTypes.string.isRequired
  }),
  location: PropTypes.shape({
    query: PropTypes.shape({
      status: PropTypes.string
    })
  }),
  gotoViewCategory: PropTypes.func.isRequired,
  editorUIData: ImmutablePropTypes.map,
  currentCharacter: ImmutablePropTypes.map,
  uiReady: PropTypes.bool.isRequired,
}

function mapStateToProps(state) {
  return {
    character: state.get('currentCharacter'),
    editorUIData: state.getIn(['ui', 'characterEditor']),
    currentCharacter: state.get('currentCharacter', Map()),
    uiReady: state.getIn(['ui', 'characterEditor', 'isReady'], false),
  }
}

function mapDispatchToProps(dispatch) {
  const actionCreators = Object.assign(characterActionCreators, characterEditorActionCreators, uiActionCreators)
  const boundActionCreators = bindActionCreators(actionCreators, dispatch)
  return Object.assign(boundActionCreators, {dispatch})

}

export const CharacterEditorContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(CharacterEditor)



