import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import shallowCompare from 'react-addons-shallow-compare'

import Dialog from 'material-ui/Dialog'
import TextField from 'material-ui/TextField'
import FlatButton from 'material-ui/FlatButton'
import { renameCharacter, closeCharacterRename } from  '../actionCreators/characterListActionCreators'


export class CharacterRename extends Component {

  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  // REACT LIFECYCLE
  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  constructor(props) {
    super(props)
    this.state = {}
  }

  componentWillReceiveProps = (nextProps) => this.setState({name: nextProps.characterName})

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  // HANDLERS
  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  handleInput = (event) => this.setState({name: event.target.value})

  handleRename = () => this.props.renameCharacter(this.props.characterId, this.state.name)

  handleClose = () => this.props.closeCharacterRename()

  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  // RENDER
  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  render() {
    const actions = [
      <FlatButton
        key="cancelRename"
        label="Cancel"
        secondary
        onTouchTap={this.handleClose}
      />,
      <FlatButton
        key="rename"
        label="Rename"
        primary
        onTouchTap={this.handleRename}
      />
    ]

    return (
      <Dialog
        title="Rename Character"
        open={this.props.characterId !== ""}
        actions={actions}
        modal={false}
        overlayStyle={styles.overlay}
        onRequestClose={this.handleClose}
        maxHeight={500}
      >
        <TextField id={this.props.characterId} value={this.state.name} onChange={this.handleInput}
          ref="name"
        />
      </Dialog>
    )
  }
}


const styles = {
  overlay: {
    backgroundColor: 'rgba(126, 126,126, 0.1)'
  }
}


CharacterRename.propTypes = {
  characterId: PropTypes.string,
  characterName: PropTypes.string,
  renameCharacter: PropTypes.func.isRequired,
  closeCharacterRename: PropTypes.func.isRequired
}

function mapStateToProps(state) {
  const characterRenameId = state.getIn(['ui', 'characterList', 'characterRenameId'], "")
  return {
    characterId: characterRenameId,
    characterName: state.getIn(['characters', characterRenameId, 'name'], "")
  }
}

export const CharacterRenameContainer = connect(
  mapStateToProps,
  {renameCharacter, closeCharacterRename}
)(CharacterRename)



