import {
  LOAD_ALL_MANUSCRIPTS_START,
  LOAD_ALL_MANUSCRIPTS_SUCCESS,
  LOAD_ALL_MANUSCRIPTS_ERROR
} from '../manuscriptListConstants'

const createGenericAction = (type) => { return {type} }

export default function () {
  return (dispatch, getState, unity) => {
    dispatch(createGenericAction(LOAD_ALL_MANUSCRIPTS_START))
    return unity.call('GetManuscripts')
      .then(manuscripts => {
        return dispatch({
          type: LOAD_ALL_MANUSCRIPTS_SUCCESS,
          payload: { manuscripts: manuscripts }
        })
      }).catch((error) => {
        return dispatch({
          type: LOAD_ALL_MANUSCRIPTS_ERROR,
          payload: {error}
        })
      })
  }
}



