import { findInstructionById, findInstructionIndexById } from '../utils/manuscriptUtils'
import { updateInstruction } from './updateInstruction'
import { RECORD_VOICE_START, RECORD_VOICE_SUCCESS } from '../manuscriptEditorConstants'

const recordVoiceStart = () => ({
  type: RECORD_VOICE_START,
  meta: {
    metrics: {
      name: 'Record Voice Start'
    }
  }
})

const recordVoiceSuccess = (dialogueId) => ({
  type: RECORD_VOICE_SUCCESS,
  payload: { dialogueId },
  meta: {
    metrics: {
      name: 'Record Voice Success'
    }
  }
})

const askMicPermission = (unity) => {
  return unity.callAsync('PermissionToUseMic')
    .catch((error) => {
      dispatch(showError('Plotagon was not allowed to access the microphone', error))
    })
}

const toggleRecordingOnOff = (dialogueId, nextStatus) => {
  return (dispatch, getState, unity) => {
    const scenes = getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    const updatedInstruction = findInstructionById(dialogueId, scenes)
      .setIn(['parameters', 'playRecording'], nextStatus)

    return updateInstruction(dialogueId, updatedInstruction)(dispatch, getState, unity)
  }
}

const startVoiceRecording = (dialogueId) => {
  return (dispatch, getState, unity) => {
    const scenes = getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    const index = findInstructionIndexById(dialogueId, scenes)

    dispatch(recordVoiceStart())
    return askMicPermission(unity)
      .then(() => {
        return unity.callAsync('RecordVoice', index)
      })
      .then(() => {
        dispatch(recordVoiceSuccess(dialogueId))
      })

  }
}

export const recordVoice = (dialogueId, playRecording) => {

  const onOrOff = playRecording ? 'off' : 'on'
  console.error('Replace this with a material ui select', onOrOff)
  return {
    type: 'SHOW_ACTION_SHEET',
    payload: {
      actions: [
        {
          title: `Turn ${onOrOff} recording`,
          action: toggleRecordingOnOff(dialogueId, !playRecording)
        },
        {
          title: `Record new voice`,
          action: startVoiceRecording(dialogueId)
        }
      ]
    }
  }
}



