import {
  OrderedMap,
  fromJS
} from 'immutable'

import { FETCH_CHARACTER_CREATOR_DATA_SUCCESS } from '../characterEditorConstants'


export default function reducer (viewData = OrderedMap({}), action) {
  switch(action.type) {

    case FETCH_CHARACTER_CREATOR_DATA_SUCCESS:
      return fetchedViewData(viewData, action.payload)

    default:
      return viewData
  }
}

const fetchedViewData = (state, payload) => {
  const views = payload.views.map((view) => [view.name, fromJS(view)])
  const genderView = fromJS(
    {
      Gender: {
        displaytype: 'gender',
        name: 'Gender',
        categories: []
      }
    })
  const nameView = fromJS(
    {
      Name: {
        displaytype: 'name',
        name: 'Name',
        categories: []
      }
    }
  )
  return OrderedMap([...genderView, ...views, ...nameView])
}



