import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import shallowCompare from 'react-addons-shallow-compare'
import { Link } from 'react-router'
import Paper from 'material-ui/Paper'
import FlatButton from 'material-ui/FlatButton'

import IconMenu from 'material-ui/IconMenu'
import MenuItem from 'material-ui/MenuItem'
import IconButton from 'material-ui/IconButton/IconButton'
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert'
import { openCharacterRename, openCharacterDelete } from  '../actionCreators/characterListActionCreators'


export default class CharacterListItem extends Component {

  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  // REACT LIFECYCLE
  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  // HANDLERS
  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleRename = () => this.props.openCharacterRename(this.props.id)

  handleDelete = () => this.props.openCharacterDelete(this.props.id)

  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  // RENDER
  //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  render() {
    return (
      <Link to={`/characters/${this.props.id}`} style={styles.link}>
        <Paper style={styles.paper}>
          <img src={this.props.image} style={styles.img} />
          <div style={styles.buttonContent}>
            <FlatButton style={styles.flatButton}
              label={this.props.name}
            />
            <IconMenu
              iconButtonElement={<IconButton><MoreVertIcon color="black" /></IconButton>}
              anchorOrigin={{horizontal: 'right', vertical: 'top'}}
              targetOrigin={{horizontal: 'right', vertical: 'top'}}
            >
              <MenuItem primaryText="Rename" onClick={this.handleRename} />
              <MenuItem primaryText="Delete" onClick={this.handleDelete} />
            </IconMenu>
          </div>
        </Paper>
      </Link>
    )
  }
}

const styles = {
  link: {
    width: '50%',
    height: 'auto',
    padding: 5
  },
  paper: {
    display: 'flex',
    flexDirection: 'column'
  },
  img: {
    width: '100%',
    borderRadius: '2px 2px 0 0'
  },
  flatButton: {
    textAlign: 'left',
    height: 49,
    width: '100%'
  },
  buttonContent: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center'
  }
}

CharacterListItem.propTypes = {
  name: PropTypes.string.isRequired,
  id: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  openCharacterRename: React.PropTypes.func.isRequired,
  openCharacterDelete: React.PropTypes.func.isRequired
}

function mapStateToProps() {
  return {}
}

export const CharacterListItemContainer = connect(
  mapStateToProps,
  {openCharacterRename, openCharacterDelete}
)(CharacterListItem)



