import { fromJS } from 'immutable'
import { uuid } from '../../../utils/uuid'

export function createScene(children = []) {
  return fromJS({
    type: 'scene',
    selected: true,
    childInstructions: children,
    charactersInScene: [],
    parameters: {
      GUID: uuid(),
      scene: undefined,
      actor1: undefined,
      actor2: undefined,
      location1: undefined,
      location2: undefined
    }
  })
}

export function createDialogue() {
  return fromJS({
    type: 'dialogue',
    selected: true,
    parameters: {
      GUID: uuid(),
      isRecorded: false,
      playRecording: false,
      expression: {
        id: 'neutral',
        text: 'Neutral'
      }
    }
  })
}

export function createAction() {
  return fromJS({
    type: 'action',
    selected: true,
    parameters: {
      GUID: uuid(),
      character: undefined,
      target: undefined,
      action: undefined
    }
  })
}

export function createSound() {
  return fromJS({
    type: 'sound',
    selected: true,
    parameters: {
      GUID: uuid()
    }
  })
}

export function createMusic() {
  return fromJS({
    type: 'music',
    selected: true,
    parameters: {
      GUID: uuid()
    }
  })
}



