import {
  Map,
  fromJS
} from 'immutable'

import {
  FETCH_EXPRESSIONS_SUCCESS
} from '../characterEditorConstants'

export default function reducer (state = Map({}), action) {
  switch(action.type) {

    case FETCH_EXPRESSIONS_SUCCESS:
      return setCharacterExpressions(state, action)

    default:
      return state
  }
}

const setCharacterExpressions = (state, action) => {
  return fromJS(action.payload.items)
}



