import { showError } from '../../UI/uiReducer'
import { push } from 'react-router-redux'
import {
  ADD_CHARACTER_START,
  ADD_CHARACTER_SUCCESS,
  ADD_CHARACTER_ERROR
} from '../characterListConstants'

export default function () {
  return (dispatch, getState, unity) => {

    dispatch({type: ADD_CHARACTER_START})
    return unity.callAsync('CreateNewCharacter', 'random')
      .then(character => {
        dispatch({
          type: ADD_CHARACTER_SUCCESS,
          payload: {character}
        })

        dispatch(push(`characters/${character.id}?status=new`))
        return character
      })
      .catch((error) => {
        dispatch({type: ADD_CHARACTER_ERROR, payload: {error}})
        dispatch(showError('Failed to create characters', error))
      })
  }
}



