import React, {Component, PropTypes} from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import ImmutablePropTypes from 'react-immutable-proptypes'

import {selectItem} from '../../../actionCreators/characterEditorActionCreators'


export class Item extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  render() {

    const {id, category, variation, item, selected} = this.props

    const handleClick = () => {
      this.context.store.dispatch(selectItem(id, variation, category, item))
    }

    const className = selected ? "item installed selected" : "item installed"
    return (
      <div className={className} onClick={handleClick}>
        <img src={this.props.url} />
      </div>
    )
  }
}

Item.propTypes = {
  variation: PropTypes.string.isRequired,
  category: PropTypes.string.isRequired,
  url: PropTypes.string.isRequired,
  id: PropTypes.string.isRequired,
  item: ImmutablePropTypes.map.isRequired,
  selected: React.PropTypes.bool
}
Item.contextTypes = {
  store: PropTypes.object
}



