import { sessionBegin, sessionEnd } from '../modules/Session/session'
import { showSubtitles, manuscriptStopped } from '../modules/ManuscriptEditor/actionCreators/playerActionCreators'
import { updateManuscriptThumbnail } from '../modules/ManuscriptList/actionCreators/manuscriptListActionCreators'
import { selectInstructionByIndex } from '../modules/ManuscriptEditor/actionCreators/selectInstruction'
import { updateCharacterThumbnail } from '../modules/CharacterList/actionCreators/updateCharacterThumbnail'

/**
 * On second thought. Don't use this :) See how it's done in setupUnity for UpdateManuscriptSelection
 *
 * Will keep it here for logging until we have replaced them all with the proper functions
 */
function dispatchAction(dispatch, eventName) {
  return function() {
    console.log("Unknown event: " + eventName + ". Translate this event to a redux action!")
  }
}

function monkeyPatchLogging(engine) {
  const originalLog = console.log
  console.log = function () {
    var stringified = null
    if (arguments.length == 1) {
      if ((typeof arguments[0] == 'string' || arguments[0] instanceof String)) {
        stringified = arguments[0]
      } else {
        stringified = JSON.stringify(arguments[0])
      }
    } else {
      stringified = JSON.stringify(arguments)
    }
    // if we send too much data in a log entry to unity it crashes/hangs
    // the unity instance, so make sure we do not
    if (stringified.length > 256) {
      stringified = stringified.substring(0, 256) + "..."
    }
    engine.call("OnLog", "JS: " + stringified)
    originalLog.apply(console, arguments)
  }
}

export default function setupUnity(dispatch, engine) {
  return new Promise( (resolve) => {
    engine.on("Ready", function() {
      monkeyPatchLogging(engine)
      engine.on('UpdateManuscriptSelection', (index) => dispatch(selectInstructionByIndex(index)))
      engine.on('StopManuscriptPlaying', () => dispatch(manuscriptStopped()))
      engine.on('SetSubtitle', () => dispatch(showSubtitles()))
      engine.on('SetThumbnail', (manuscriptId, thumbnail) => dispatch(updateManuscriptThumbnail(manuscriptId, thumbnail)))
      engine.on("FinalizeExport", dispatchAction(dispatch, "FinalizeExport"))
      engine.on("CancelExport", dispatchAction(dispatch, "CancelExport"))
      engine.on("ExportCompleted", dispatchAction(dispatch, "ExportCompleted"))
      engine.on("UploadCompleted", dispatchAction(dispatch, "UploadCompleted"))
      engine.on("AuthRedirect", dispatchAction(dispatch, "AuthRedirect"))
      engine.on("TweetDialogueComplete", dispatchAction(dispatch, "TweetDialogueComplete"))
      engine.on("FacebookDialogueComplete", dispatchAction(dispatch, "FacebookDialogueComplete"))
      engine.on("CheckContentOnStartup", dispatchAction(dispatch, "CheckContentOnStartup"))
      engine.on("OrientationChanged", dispatchAction(dispatch, "OrientationChanged"))
      engine.on("CloseManuscript", dispatchAction(dispatch, "CloseManuscript"))
      engine.on("SetCharacterThumbnail", (id, icon) => dispatch(updateCharacterThumbnail(id, icon)))

      engine.on('appEnteredBackground', function() {
        dispatch(sessionEnd())
      })

      engine.on('appBecameActive', function() {
        dispatch(sessionBegin())
      })

      engine.on('appTerminated', function() {
        dispatch(sessionEnd())
      })

      engine.call('UIInitialized')

      resolve()
    })
  })
}



