import {Map} from 'immutable'

import {uuid} from '../../utils/uuid'

export const SESSION_BEGIN = "SESSION_BEGIN"
export const SESSION_END = "SESSION_END"

export default function reducer (session = Map({}), action) {
  const payload = action.payload
  switch(action.type) {
    case SESSION_BEGIN:
      return beginSessionReducer (payload.id, payload.start)

    case SESSION_END:
      return Reducer (payload.newId, payload.stop)

    default:
      return session
  }
}

function beginSessionReducer(id, start) {
  return Map({ "id": id, "start": start })
}

function Reducer(newId, stop) {
  return Map({ "id": newId, "start": stop })
}

export function sessionBegin() {
  // Amplitude needs this id to be a timestamp, so don't switch it for a uuid for instance
  const id = new Date().getTime()
  const start = id
  return {
    type: SESSION_BEGIN,
    payload: {
      id: id,
      start: start
    },
    meta: {
      metrics: {
        name: 'Session Start'
      }
    }
  }
}

export function sessionEnd() {
  return (dispatch, getState) => {
    const start = getState().getIn(['session', 'start'], new Date().getTime())
    const stop = new Date().getTime()
    const newId = uuid()
    const duration = (stop - start) / 1000

    return {
      type: SESSION_END,
      payload: {
        newId: newId,
        stop: stop
      },
      meta: {
        metrics: {
          name: 'Session End',
          data: {
            length: duration
          }
        }
      }
    }
  }

}



