import React, { PropTypes, Component } from 'react'
import { connect } from 'react-redux'
import shallowCompare from 'react-addons-shallow-compare'
import ImmutablePropTypes from 'react-immutable-proptypes'
import { List } from 'immutable'

import IconButton from 'material-ui/IconButton'
import CameraAsset from 'material-ui/svg-icons/image/photo-camera'

import ExpressionsSelect from './ExpressionsSelect'
import { CharacterPhotoContainer } from './CharacterPhoto'
import * as actions from  '../../actionCreators/characterEditorActionCreators'

export class PreviewPane extends Component {

  constructor(props) {
    super(props)
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  handleExpressionChanged = (selection) => this.props.setCharacterExpression(selection)

  handleCameraClick = () => this.props.openCharacterPhoto()

  render() {
    return (
      <div className="preview-pane coui-noinput" style={this.props.isUsingMocks ? styles.background : null}>
        <div className="show-sharacter-preview preview-pane-inner coui-noinput">
          <div style={styles.buttonContainer}>
            <IconButton id="cameraButton"
              onClick={this.handleCameraClick}
              iconStyle={styles.mediumIcon}
              style={styles.medium}
            >
              <CameraAsset color="white" />
            </IconButton>
            <CharacterPhotoContainer />
            <ExpressionsSelect expressions={this.props.expressions}
              onChange={this.handleExpressionChanged}
            />
          </div>
          <button className="custom absolute-icon icon-close close" onClick={this.props.handleGoBack} />
        </div>
      </div>
    )
  }
}

const styles = {
  medium: {
    width: 48,
    height: 48,
    padding: 0
  },
  mediumIcon: {
    width: 36,
    height: 36
  },
  buttonContainer: {
    height: '100%',
    display: 'flex',
    flexDirection: 'row-reverse',
    alignItems: 'flex-end'
  },
  background: {
    backgroundImage: 'url(img/mock/characterEditor.png)',
    backgroundSize: 'cover'
  }
}

PreviewPane.propTypes = {
  handleGoBack: PropTypes.func.isRequired,
  setCharacterExpression: PropTypes.func.isRequired,
  openCharacterPhoto: PropTypes.func.isRequired,
  isUsingMocks: PropTypes.bool.isRequired,
  expressions: ImmutablePropTypes.list.isRequired,
}

function mapStateToProps(state) {
  return {
    expressions: state.get('characterExpressions', List()),
    isUsingMocks: state.getIn(['appConfig', 'mockedUnity'], false)
  }
}

export const PreviewPaneContainer = connect(
  mapStateToProps,
  actions
)(PreviewPane)



