import { findInstructionIndexById } from '../utils/manuscriptUtils'
import { UPDATE_INSTRUCTION } from '../manuscriptEditorConstants'
import { stripVisualData } from '../utils/instructionUtils'
import { fromJS, Map } from 'immutable'

/**
 *
 * @param instructionId {string}  The instruction id
 * @param instruction   {object}  The updated instruction
 */
export const updateScene = (instructionId, instruction) => {
  return (dispatch, getState, unity) => {

    if (!Map.isMap(instruction)) throw "Scene has to be a map"

    const index = findInstructionIndexById(
      instructionId,
      getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    )
    const content = getState().get('content').toJS()

    unity.call('UpdateInstruction', stripVisualData(instruction), index)
    dispatch({
      type: UPDATE_INSTRUCTION,
      payload: {instruction: instruction.toJS(), instructionId, content}
    })
  }
}

/**
 *
 * @param instructionId {string}  The instruction id
 * @param instruction   {object}  The updated instruction
 */
export const updateInstruction = (instructionId, instruction) => {
  return (dispatch, getState, unity) => {

    if (!Map.isMap(instruction)) throw "Instruction has to be a map"

    const index = findInstructionIndexById(
      instructionId,
      getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    )
    const content = getState().get('content').toJS()

    unity.call('UpdateInstruction', stripVisualData(instruction), index)
    dispatch({
      type: UPDATE_INSTRUCTION,
      payload: {instruction: instruction.toJS(), instructionId, content}
    })
  }
}

const updateSceneChildInstructions = (childInstructions, prevCharacter, newCharacter) => {

  return (dispatch, getState, unity) => {

    // We only need to update actions & dialogues
    const affectedInstructions = childInstructions.filter(i => i.get('type') === 'action' || i.get('type') === 'dialogue')

    // Trigger updates for all affected instructions
    affectedInstructions.forEach(i => {
      const {shouldUpdate, updatedInstruction} = replaceCharacterInInstruction(i, prevCharacter, newCharacter)
      if (shouldUpdate) {
        updateInstruction(i.getIn(['parameters', 'GUID']), updatedInstruction)(dispatch, getState, unity)
      }
    })
  }
}

// Mmmm, mutable state. Careful with this one.
/* eslint-disable */
const replaceCharacterInInstruction = (instruction, prevCharacter, newCharacter) => {

  var mutableInstruction = stripVisualData(instruction)
  var shouldUpdate = false

  if (prevCharacter !== undefined) {
    // We might find the replaced character at multiple locations depending on the type of instruction
    if (instruction.getIn(['parameters', 'character', 'id']) === prevCharacter.get('id')) {
      mutableInstruction.parameters.character = {id: newCharacter.get('id'), text: newCharacter.get('name')}
      shouldUpdate = true
    }
    if (instruction.getIn(['parameters', 'target', 'id']) === prevCharacter.get('id')) {
      mutableInstruction.parameters.target = {id: newCharacter.get('id'), text: newCharacter.get('name')}
      shouldUpdate = true
    }
    if (instruction.getIn(['parameters', 'actor1', 'id']) === prevCharacter.get('id')) {
      mutableInstruction.parameters.actor1 = {id: newCharacter.get('id'), text: newCharacter.get('name')}
      shouldUpdate = true
    }
    if (instruction.getIn(['parameters', 'actor2', 'id']) === prevCharacter.get('id')) {
      mutableInstruction.parameters.actor2 = {id: newCharacter.get('id'), text: newCharacter.get('name')}
      shouldUpdate = true
    }
  }

  return {
    shouldUpdate: shouldUpdate,
    updatedInstruction: fromJS(mutableInstruction)
  }
}
/* eslint-enable */

const findCharacterById = (characterId, getState) => {
  return getState()
    .getIn(['content', 'characters'])
    .find(c => c.get('id') === characterId)
}


export const replaceCharacterInScene = (sceneInstructionId, newCharacterId, prevCharacterId, locationKey) => {
  return (dispatch, getState, unity) => {

    const sceneIndex = findInstructionIndexById(
      sceneInstructionId,
      getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    )
    const prevCharacter = findCharacterById(prevCharacterId, getState)
    const newCharacter = findCharacterById(newCharacterId, getState)
    const content = getState().get('content').toJS()

    const updatedSceneInstruction = getState()
      .getIn(['currentManuscript', 'manuscript', 'instructions'])
      .find(i => i.getIn(['parameters', 'GUID']) === sceneInstructionId)
      .setIn(['parameters', locationKey], {id: newCharacter.get('id'), text: newCharacter.get('name')})


    unity.call('UpdateInstruction', stripVisualData(updatedSceneInstruction), sceneIndex)
    dispatch({
      type: UPDATE_INSTRUCTION,
      payload: {
        instruction: updatedSceneInstruction.toJS(),
        instructionId: sceneInstructionId,
        content: content
      }
    })

    updateSceneChildInstructions(updatedSceneInstruction.get('childInstructions'), prevCharacter, newCharacter)(dispatch, getState, unity)
  }
}




