import {
  RENAME_MANUSCRIPT_START,
  RENAME_MANUSCRIPT_SUCCESS,
  RENAME_MANUSCRIPT_ERROR
} from '../manuscriptListConstants'

export default function (id, newName) {
  return (dispatch, getState, unity) => {
    dispatch({type: RENAME_MANUSCRIPT_START})
    return unity.call('RenameManuscript', id, newName)
      .then(() => {
        return dispatch({
          type: RENAME_MANUSCRIPT_SUCCESS,
          payload: { id, newName }
        })
      }).catch((error) => {
        return dispatch({
          type: RENAME_MANUSCRIPT_ERROR,
          payload: { error }
        })
      })
  }
}



