import { findInstructionByIndex, findInstructionIndexById } from '../utils/manuscriptUtils'
import { SELECT_INSTRUCTION, SELECT_CURRENT_SCENE } from '../manuscriptEditorConstants'

export const newSelection = (instructionId) => {
  return {
    type: SELECT_INSTRUCTION,
    payload: {instructionId}
  }
}

export const selectInstructionByIndex = (index) => {
  return (dispatch, getState, unity) => {
    const instruction = findInstructionByIndex(index, getState)
    unity.call('UpdateCurrentManuscriptSelection', index)
    dispatch(newSelection(instruction.getIn(['parameters', 'GUID'])))
  }
}

export const selectInstructionById = (instructionId) => {
  return (dispatch, getState, unity) => {
    const index = findInstructionIndexById(
      instructionId,
      getState().getIn(['currentManuscript', 'manuscript', 'instructions'])
    )
    unity.call('UpdateCurrentManuscriptSelection', index)
    dispatch(newSelection(instructionId))
  }
}

export const selectCurrentSceneById = (sceneId) => {
  return {
    type: SELECT_CURRENT_SCENE,
    payload: { sceneId }
  }
}

export const selectFirstInstruction = () => {
  return (dispatch, getState, unity) => {

    const scenes = getState().getIn(['currentManuscript', 'manuscript', 'instructions'])

    if (!scenes.isEmpty()) {
      selectInstructionByIndex(0)(dispatch, getState, unity)
    }
  }
}



