import React, {Component, PropTypes} from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import ImmutablePropTypes from 'react-immutable-proptypes'

import Variation from './Variation'

export class Variations extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  createVariations = (variations) => {
    const {selection} = this.props
    return variations.map(variation => {
      const id = variation.get('id')
      const url = variation.getIn(['icon', id])
      return (
        <Variation key={id}
          id={id}
          url={url}
          selected={id === selection}
          category={this.props.category}
        />
      )
    })
  }

  render() {
    const {variations} = this.props
    return (
      <div className="variations">
        {this.createVariations(variations)}
      </div>
    )
  }
}

Variations.propTypes = {
  variations: ImmutablePropTypes.list.isRequired,
  selection: PropTypes.string.isRequired,
  category: PropTypes.string.isRequired
}



