import { showError } from '../../UI/uiReducer'
import {
  OPEN_CHARACTER_PHOTO_START,
  OPEN_CHARACTER_PHOTO_SUCCESS,
  OPEN_CHARACTER_PHOTO_ERROR
} from '../characterEditorConstants'

export function openCharacterPhoto() {
  return (dispatch, getState, unity) => {
    dispatch({type: OPEN_CHARACTER_PHOTO_START})
    const width = 650
    const height = 960
    return unity.callAsync('GrabPreviewFrame', width, height)
      .then(data => {
        return dispatch({
          type: OPEN_CHARACTER_PHOTO_SUCCESS,
          payload: {
            thumb: data.thumbnail,
            path: data.path
          }
        })
      })
      .catch((error) => {
        console.error(error)
        dispatch({type: OPEN_CHARACTER_PHOTO_ERROR})
        dispatch(showError('Failed to open character photo', error))
      })
  }
}



