import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import ImmutablePropTypes from 'react-immutable-proptypes'
import {Map} from 'immutable'

import { Item } from './Item.jsx'
import { Variations } from './Variations'

export default class NormalItemPicker extends Component {

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  getCurrentSelection = () => this.props.currentSelection || null

  createItems = (category) => {
    return category
      .get('items')
      .map(item => {
        const id = item.get('id')
        return (
          <Item key={id}
            url={item.get('icons').first()}
            id={id}
            selected={id === this.getCurrentSelection().id}
            variation={item.get('variations').get(0, Map()).get('id', '')}
            item={item}
            category={this.props.category.get('category')}
          />
        )
      })
  }

  createVariations = (variations) => {
    return variations
      ? (
        <Variations variations={this.props.variations}
          category={this.props.category.get('category')}
          selection={this.getCurrentSelection().variation}
        />
      )
      : null
  }

  render () {
    return (<div className="picker has-variations">
      <div className="items">
        {this.createItems(this.props.category)}
      </div>
      {this.createVariations(this.props.variations)}
    </div>)
  }
}

NormalItemPicker.propTypes = {
  variations: ImmutablePropTypes.list.isRequired,
  category: ImmutablePropTypes.map.isRequired,
  currentSelection: PropTypes.shape({
    id: React.PropTypes.string.isRequired,
    variation: React.PropTypes.string.isRequired
  })
}



