import { showError } from '../../UI/uiReducer'
import {
  SET_CHARACTER_NAME_START,
  SET_CHARACTER_NAME_SUCCESS,
  SET_CHARACTER_NAME_ERROR
} from '../characterEditorConstants'


export function setName(id, name) {
  return (dispatch, getState, unity) => {
    dispatch({
      type: SET_CHARACTER_NAME_START
    })
    return unity.callAsync('RenameCharacter', id, name)
      .then(() => {
        dispatch({
          type: SET_CHARACTER_NAME_SUCCESS,
          payload: { id, name }
        })
      }).catch((error) => {
        dispatch({type: SET_CHARACTER_NAME_ERROR, payload: {error}})
        dispatch(showError('Failed to set name on character', error))
      })
  }
}



