import React, { Component, PropTypes } from 'react'
import shallowCompare from 'react-addons-shallow-compare'
import {connect} from 'react-redux'
import {List} from 'immutable'
import ImmutablePropTypes from 'react-immutable-proptypes'
import { loadManuscript } from '../actionCreators/loadManuscript'
import ToolBar from './ToolBar'

import { PreviewWindowContainer } from './PreviewWindow'
import Share from './Share'
import SceneList from './SceneList'



export class ManuscriptEditor extends Component {

  constructor(props) {
    super(props)
  }

  componentWillMount() {
    const plotId = this.props.params.plotId
    this.context.store.dispatch(loadManuscript(plotId))
  }

  shouldComponentUpdate = (nextProps, nextState) => shallowCompare(this, nextProps, nextState)

  render() {

    const hasSceneInstruction = this.props.instructions.some(i => i.get('type') === 'scene')

    return (

      <div style={styles.container}>
        <Share
          playerState={this.props.playerState}
        />
        <PreviewWindowContainer />
        <ToolBar
          hasSceneInstruction={hasSceneInstruction}
        />
        <SceneList
          sceneInstructions={this.props.instructions}
          currentSceneId={this.props.currentSceneId}
          content={this.props.content}
          editMode={this.props.editMode}
          playerState={this.props.playerState}
        />
    </div>
    )
  }
}

const styles = {
  container: {
    width: '100%',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  }
}

ManuscriptEditor.propTypes = {
  currentSceneId: PropTypes.string,
  params: PropTypes.shape({
    plotId: PropTypes.string.isRequired
  }),
  instructions: ImmutablePropTypes.list,
  content: ImmutablePropTypes.contains({
    scenes: ImmutablePropTypes.list.isRequired,
    characters: ImmutablePropTypes.list.isRequired,
    actions: ImmutablePropTypes.list.isRequired,
    expressions: ImmutablePropTypes.list.isRequired,
    music: ImmutablePropTypes.list.isRequired,
    sounds: ImmutablePropTypes.list.isRequired
  }),
  editorUIData: ImmutablePropTypes.map,
  editMode: PropTypes.bool,
  playerState: PropTypes.string,
  location: PropTypes.shape({
    pathname: PropTypes.string
  })
}
ManuscriptEditor.contextTypes = {
  store: PropTypes.object
}

function mapStateToProps(state) {
  return {
    instructions: state.getIn(['currentManuscript', 'manuscript', 'instructions'], List([])),
    currentSceneId: state.getIn(['currentManuscript', 'manuscript', 'currentSceneId']),
    content: state.get('content'),
    editorUIData: state.getIn(['ui', 'manuscriptEditor']),
    editMode: state.getIn(['currentManuscript', 'editMode']),
    playerState: state.getIn(['currentManuscript', 'playerState'], '')
  }
}

export const ManuscriptEditorContainer = connect(
  mapStateToProps
)(ManuscriptEditor)



