import { Map, fromJS } from 'immutable'
import {
  LOAD_ALL_MANUSCRIPTS_SUCCESS,
  RENAME_MANUSCRIPT_SUCCESS,
  DELETE_MANUSCRIPT_SUCCESS,
  ADD_MANUSCRIPT_SUCCESS
} from '../manuscriptListConstants'

export default function reducer (manuscripts = Map({}), action) {
  const payload = action.payload

  switch(action.type) {
    case LOAD_ALL_MANUSCRIPTS_SUCCESS:
      return loadAllManuscripts(manuscripts, payload)

    case RENAME_MANUSCRIPT_SUCCESS:
      return renameManuscript(manuscripts, payload)

    case DELETE_MANUSCRIPT_SUCCESS:
      return deleteManuscript(manuscripts, payload)

    case ADD_MANUSCRIPT_SUCCESS:
      return addManuscript(manuscripts, payload)

    default:
      return manuscripts
  }
}

function loadAllManuscripts(manuscripts, payload) {
  return manuscripts.merge(
    payload.manuscripts.reduce((acc, manuscript) => {
      return acc.set(manuscript.id, fromJS(manuscript))
    }, Map({})).reverse()
  )
}

function renameManuscript(manuscripts, payload) {
  return manuscripts
    .setIn([payload.id, 'name'], payload.name)
}

function deleteManuscript(manuscripts, payload) {
  return manuscripts.delete(payload.id)
}

function addManuscript(state, payload) {
  return state.set(payload.manuscript.id, fromJS(payload.manuscript))
}



